import React from 'react';
import { DateTimePickerToolbar } from '@mui/x-date-pickers/DateTimePicker';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import dayjs from 'dayjs';

const CustomDateTimePickerToolbar = (props) => {
    const { setOpenView, setView, onChange, value, ...other } = props;

    const handleTodayButtonClick = () => {
        const today = dayjs();
        onChange(today);
    };

    return (
        <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
            <DateTimePickerToolbar {...props} />
            <Button onClick={handleTodayButtonClick} variant="outlined" size="small" style={{ margin: '8px', position: 'absolute', bottom: "2.5px", left: "2.5px" }}>
                Today
            </Button>
        </Box>
    );
};

export default CustomDateTimePickerToolbar;
