import React, { useRef } from "react";
import { Store } from "app/redux/Store";
import { useSelector, useDispatch, shallowEqual } from 'react-redux';
import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@mui/material';
import {
    update_generalValues,
} from 'app/redux/actions/MCODetailsAction';
import MaterialForm from 'app/components/McoPages/MaterialForm';

const getAffectedPartsData = async () => {
    const getStorevalue = Store.getState();
    const responseData = getStorevalue?.mcoDetails?.responseData || {};
    const affectedPartsData = getStorevalue?.mcoDetails?.affectedPartsData || [];
    if (affectedPartsData?.length) {
        return affectedPartsData || [];
    } else {
        return responseData?.mcoaffectedItem || [];
    }
}
const MaterialAttributeDialogBox = () => {
    const {
        selectedRowData,
        selectedRowFormdata: attributeformData,
        responseData,
        showMaterialForm,
    } = useSelector((state) => ({
        selectedRowData: state?.mcoDetails?.selectedRowData,
        selectedRowFormdata: state?.mcoDetails?.selectedRowFormdata,
        responseData: state?.mcoDetails?.responseData,
        showMaterialForm: state?.mcoDetails?.showMaterialForm,
    }), shallowEqual);
    const dispatch = useDispatch();
    const childCompRef = useRef({});

    const saveMaterialDetails = async (formvalue) => {
        const affectedPartsData = await getAffectedPartsData();
        const mcoaffectedItem = [...affectedPartsData];
        const findSaveIndex = mcoaffectedItem.findIndex((material, materialIndex) => {
            return (selectedRowData?.id === material.id);
        });

        mcoaffectedItem[findSaveIndex]['materialType'] = {
            name: mcoaffectedItem[findSaveIndex]['materialType'].name,
            attributeMap: formvalue
        };

        dispatch(update_generalValues({
            affectedPartsData: mcoaffectedItem,
            isMaterialFormEdited: true,
            showMaterialForm: false
        }));
    }
    return (
        <Dialog
            open={showMaterialForm}
            onClose={() => {
                dispatch(update_generalValues({ showMaterialForm: false }));
            }}
            scroll={'paper'}
            aria-labelledby="scroll-dialog-title"
            aria-describedby="scroll-dialog-description"
            maxWidth={'md'}
            fullWidth={true}
        >
            <DialogTitle id="scroll-dialog-title">Material Details</DialogTitle>
            <DialogContent dividers={true}>
                <DialogContentText id="scroll-dialog-description" tabIndex={-1}>
                    <MaterialForm ref={childCompRef} saveMaterialDetails={saveMaterialDetails} />
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button onClick={() => {
                    dispatch(update_generalValues({ showMaterialForm: false }));
                }}>Cancel</Button>
                <Button
                    onClick={() => { childCompRef.current.callsubmitForm(); }}
                    disabled={(["Review", "Release", "Implemented"].includes(responseData?.mcoaffectedItem?.mcostate) || (!Object.keys(attributeformData || {}).length))}
                >Save</Button>
            </DialogActions>
        </Dialog>
    )
}

export default MaterialAttributeDialogBox;