import React, { useState, useEffect } from 'react';
import { useSelector, shallowEqual } from 'react-redux';
import VerticalLinearStepper from 'app/components/McoPages/TimelineListView';
import { Breadcrumb, SimpleCard } from "app/components";
import {
    Box,
    Grid,
    styled,
    Tooltip
} from "@mui/material";
import PropTypes from 'prop-types';
import Loader from 'app/components/Loader';
import './mcoStyles.css';

const Container = styled("div")(({ theme }) => ({
    margin: "30px",
    [theme.breakpoints.down("sm")]: { margin: "16px" },
    "& .breadcrumb": {
        marginBottom: "30px",
        [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
    },
}));

const capitalize = s => s && s[0].toUpperCase() + s.slice(1)

function FormRow(props) {
    const labelArr = {
        rme: "Responsible Manufacturing Engineer",
        rsme: "Responsible Senior Manufacturing Engineer",
        mcostate: "State",
        responseFromErp: "Export Status",
        plantName: "Plant",
    };

    if (props.label === 'description') {
        return (
            <>
                <Grid item md={6} sm={6} xs={6} key={`${props.label}-label`}>
                    Description
                    <span style={{ float: "right" }}>:</span>
                </Grid>
                <Grid item md={6} sm={6} xs={6} key={props.label}>
                    <Tooltip
                        title={
                            <div style={{ maxHeight: '300px', overflowY: 'scroll' }}>
                                {props.value}
                            </div>
                        }
                        arrow
                        placement="top"
                    >
                        <div
                            style={{
                                textOverflow: 'ellipsis',
                                overflow: 'hidden',
                                whiteSpace: 'nowrap',
                                maxWidth: '300px', // Adjust the max width as needed
                                cursor: 'pointer'
                            }}
                        >
                            {props.value.length > 50 ? `${props.value.substring(0, 50)}...` : props.value}
                         </div>
                    </Tooltip>
                </Grid>
            </>
        );
    }

    return (
        <>
            <Grid item md={6} sm={6} xs={6} key={props.label}>
                {Object.keys(labelArr).includes(props.label)
                    ? capitalize(labelArr[props.label])
                    : capitalize(props.label)}
                <span style={{ float: "right" }}>:</span>
            </Grid >
            <Grid item md={6} sm={6} xs={6}>
                {(props.value !== "") ? props.value : "-"}
            </Grid>
        </>
    );
}

const TrackerFormView = ({ isLoading, mcodetails, activeStep, currentMCOTrackerdetails }) => {
    if (isLoading) {
        return true;
    }
    return (
        (mcodetails) ? (
            <Grid
                container
                spacing={1}
                alignItems="center"
            >
                <Grid item md={6} sm={12} xs={12}>
                    <Grid
                        container
                        spacing={1}
                        alignItems="center"
                        key="samplekeys"
                    >
                        <Grid container spacing={2} alignItems="start" className="caDetailsContainer" key="samplekeys2">
                            {
                                (mcodetails && Object.keys(mcodetails?.mcoPropeties)?.length) ? (
                                    Object.keys(mcodetails?.mcoPropeties)?.map((eitem) => {
                                        if (
                                            [
                                                "id",
                                                "type",
                                                "physicalid",
                                                "businessUnit",
                                                "organizationId",
                                                "responseFromErp",
                                                "rmeEmailAddress",
                                                "rsmeEmailAddress",
                                                "errorMsg",
                                            ].includes(eitem)
                                        ) {
                                            return false;
                                        }
                                        return (
                                            <FormRow key={eitem} label={eitem} value={mcodetails?.mcoPropeties[eitem]} />
                                        )
                                    })
                                ) : (
                                    <></>
                                )
                            }
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item md={6} sm={12} xs={12} style={{ textAlign: "right" }}>
                    <VerticalLinearStepper activeStepValue={activeStep} currentMCOTrackerdetails={currentMCOTrackerdetails} />
                </Grid>
            </Grid>
        ) : (
            <div style={{ textAlign: "center", paddingBottom: "10px" }}>
                Result not found!
            </div>
        )
    )
}

const TrackerProgressBar = (props) => {
    const { lifeCycleData, responseData } = useSelector((state) => ({
        lifeCycleData: state.mcoDetails.lifeCycleData,
        responseData: state.mcoDetails.responseData,
    }), shallowEqual);
    const [isLoading, setIsLoading] = useState(false);
    const [mcodetails, setMcodetails] = useState(null);
    const [currentMCOTrackerdetails, setCurrentMCOTrackerdetails] = useState(null);
    const [activeStep, setActiveStep] = useState({});

    const prepareLifeCycleData = (resultData, currentmcoName) => {
        setIsLoading(true);
        setMcodetails(responseData);
        const mcoStateVal = responseData?.mcoPropeties?.mcostate?.toLowerCase();

        if (Object.keys(resultData).length === 0) {
            setIsLoading(false);
            return;
        }

        const currectMcoLifeCycleData = resultData[currentmcoName] || {};
        // if (Object.keys(currectMcoLifeCycleData).length === 0) {
        //     setIsLoading(false);
        //     return;
        // }

        let findStepper = 0;
        let findStatus = {};
        let skipCustomStage = [];

        if (!Object.keys(currectMcoLifeCycleData)?.length && ["release"].includes(mcoStateVal)) {
            findStepper = 4;
            findStatus = { type: 'release', status: 'success' };
            skipCustomStage = ["item", "bos", "bom"];
        } else if (Object.keys(currectMcoLifeCycleData)?.length) {
            const isSkipped = false;
            if (currectMcoLifeCycleData["promoted"] === "skipped" || isSkipped) {
                findStepper = 4;
                findStatus = { type: 'release', status: 'success' };
                skipCustomStage = ["item", "bos", "bom"];
            } else {
                for (const sitem of Object.keys(currectMcoLifeCycleData)) {
                    if (Object.keys(findStatus).length && findStatus.status.includes("failed")) continue;

                    const status = currectMcoLifeCycleData[sitem];

                    if (sitem === "itemStatus" && status === "skipped") {
                        findStepper += 1;
                        skipCustomStage.push("item");
                        continue;
                    }
                    if (sitem === "bosStatus" && status === "skipped") {
                        findStepper += 1;
                        skipCustomStage.push("bos");
                        continue;
                    }
                    if (sitem === "bomStatus" && status === "skipped") {
                        findStepper += 1;
                        skipCustomStage.push("bom");
                        continue;
                    }

                    if (["itemStatus", "bosStatus", "bomStatus", "releaseStatus"].includes(sitem) && ["inprogress", "success", "failed"].includes(status)) {
                        if (["success"].includes(status)) {
                            findStepper += 1;
                            // if (sitem === "bomStatus" && ["release", "implemented"].includes(mcoStateVal)) {
                            //     findStepper += 1;
                            // }
                        }
                        findStatus = { type: sitem.replace("Status", ""), status };
                    }
                }

                // Checks applied for handling old data 
                if (!Object.keys(currectMcoLifeCycleData || {})?.includes('releaseStatus')) {
                    if (findStepper == 3 && ["release", "implemented"].includes(mcoStateVal)) {
                        findStepper += 1;
                    }
                }
            }
        }

        setActiveStep({ stepperValue: findStepper, currentStatus: findStatus, skippedstage: skipCustomStage });
        setCurrentMCOTrackerdetails(currectMcoLifeCycleData); // Adding this line to set current MCO tracker details
        setIsLoading(false);
    };

    useEffect(() => {
        const currentmcoName = props?.mcoName;
        prepareLifeCycleData(lifeCycleData, currentmcoName);
    }, [lifeCycleData, responseData]);

    return (
        <Container>
            {
                (!props?.fromPage) ? (
                    <>
                        <Box className="breadcrumb">
                            <Breadcrumb routeSegments={[{ name: "MCO Status Tracker" }]} />
                        </Box>
                        <SimpleCard>
                            <TrackerFormView isLoading={isLoading} mcodetails={mcodetails} activeStep={activeStep} currentMCOTrackerdetails={currentMCOTrackerdetails} />
                        </SimpleCard>
                    </>
                ) : (
                    <TrackerFormView isLoading={isLoading} mcodetails={mcodetails} activeStep={activeStep} currentMCOTrackerdetails={currentMCOTrackerdetails} />
                )
            }
            {isLoading ? (
                <div style={{ textAlign: 'center' }}>
                    <Loader show={isLoading} />
                </div>
            ) : <></>}
        </Container>
    );
};

FormRow.propTypes = {
    label: PropTypes.node.isRequired,
    value: PropTypes.node.isRequired
};
TrackerProgressBar.propTypes = {
    mcoName: PropTypes.node.isRequired,
    fromPage: PropTypes.node.isRequired,
};

export default TrackerProgressBar;