import React, { useEffect, useState, useRef, useMemo } from "react";
import { Tabs, Tab, Typography, Box, Link, Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Grid, styled } from '@mui/material';
import shortId from 'shortid';
import { Breadcrumb, SimpleCard } from "app/components";
import { MCOService } from 'services/mcoService';
import { useLocation, useParams } from 'react-router-dom';
import Loader from 'app/components/Loader';
import { useDispatch, useSelector, shallowEqual } from 'react-redux';
import { Store } from "app/redux/Store";
import {
    updateResponseData,
    updateMcoDetails,
    update_generalValues,
    resetValues
} from 'app/redux/actions/MCODetailsAction';
import { updateGlobalBUandInstance } from "app/redux/actions/GlobalBUandInstanceAction";
import AffectedPartsTable from 'app/components/McoPages/AffectedPartsTable';
import LifeCycleStepperComponent from 'app/components/McoPages/LifeCycleStepper';
import PropertiesForm from "app/components/McoPages/PropertiesForm";
import CustomAlertBox from "app/components/McoPages/CustomAlertBox";
import SomethingWrong from "app/components/McoPages/PageError";
import SaveButtons from "app/components/McoPages/PartsSaveButton";
import HistoryDialogBox from "app/components/McoPages/HistoryDialogBox";
import RefreshButtons from "app/components/McoPages/RefreshButtons";
import TrackerHistoryLinks from "app/components/McoPages/TrackerHistoryLinks";
import TrackerProgressBar from "./TrackerProgressBar";
import generalInfo from "app/utils/mcoUtils/generalInfo.json";
import PropTypes from 'prop-types';
import {
    fetchRSMEAndRMEDetails,
    CloneDataFromMaster,
    prepareItemPayload,
    prepareFormData,
    validationChecks,
    initialMRFunctions,
    collectSourcePlantTemplate,
    updateMcoDetailsInDatabase,
    prepareAffectedPartData,
    exportMCOData,
    getAffectedPartsData,
    getIsMaterialFormEdited,
    handleErrorForThrows
} from "app/utils/mcoUtils/utilities";
import {
    dropdownValidation,
    getRDOOUOrgSetMappings
} from "app/utils/mcoUtils/dropdownValidation";
// import {
//     dropdownValidationSAP,
// } from "app/utils/mcoUtils/dropdownValidationSAP";
import { useGetDropdownOption } from "app/hooks/mcoHooks/useGetDropdownOption";
import BulkUploadPartAndTemplatePupup from "app/components/bulkUploadPartAndTemplatePupup";
import "./mcoStyles.css";
import SaaSLifeCycleStepperComponent from "app/components/McoPages/SaaSLifeCycleStepperComponent";

const Container = styled("div")(({ theme }) => ({
    margin: "30px",
    [theme.breakpoints.down("sm")]: { margin: "16px" },
    "& .breadcrumb": {
        marginBottom: "30px",
        [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
    },
}));

function TabPanel(props) {
    return (
        <div
            role="tabpanel"
            hidden={props?.value !== props?.index}
            id={`simple-tabpanel-${props.index}`}
            aria-labelledby={`simple-tab-${props?.index}`}
            {...props.other}
        >
            {props.value === props.index && (
                <Box sx={{ p: 3 }}>
                    <Typography>{props.children}</Typography>
                </Box>
            )}
        </div>
    );
}

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

const Mcodetails = () => {
    const getDropdownOptions = useGetDropdownOption();
    const dispatch = useDispatch();
    const {
        isPageHavingIssue,
        responseData,
        templates: templateList,
        templateMappingsInfo,
        lifeCycleData,
        systemSetingsDetail,
        mrDetails,
        RSMEList,
        RMEList,
        isPropertyFormEdited,
        eachTemplateOptions,
        materialData,
        updatedRoutes,
    } = useSelector((state) => ({
        cc: state.mcoDetails.isPageHavingIssue,
        responseData: state.mcoDetails.responseData,
        templates: state.mcoDetails.templates,
        templateMappingsInfo: state.mcoDetails.templateMappingsInfo,
        lifeCycleData: state.mcoDetails.lifeCycleData,
        systemSetingsDetail: state.mcoDetails.systemSetingsDetail,
        mrDetails: state.mcoDetails.mrDetails,
        RSMEList: state.mcoDetails.RSMEList,
        RMEList: state.mcoDetails.RMEList,
        isPropertyFormEdited: state.mcoDetails.isPropertyFormEdited,
        eachTemplateOptions: state.mcoDetails.eachTemplateOptions,
        materialData: state.mcoDetails.materialData,
        updatedRoutes: state.mcoDetails.updatedRoutes,
    }), shallowEqual);
    const [tapvalue, setTapvalue] = useState(0);
    const [masterData, setMasterData] = useState(null);
    const [isLoading, setIsLoading] = useState(true);
    const [itemValidationArr, setItemValidationArr] = useState([]);
    const [showTrackerBox, setShowTrackerBox] = useState(false);
    const [showHistoryBox, setShowHistoryBox] = useState(false);
    const [lifeCycleStep, setLifeCycleStep] = useState('Start');
    const [stepData, setStepData] = useState(null);
    const [openAlert, setOpenAlert] = useState(false);
    const [openDesc, setOpenDesc] = useState(null);
    const [initialLoad, setInitialLoad] = useState(false);
    const [plantType, setPlantType] = useState(localStorage.getItem("plantType") || "");
    useEffect(() => {
        return () => localStorage.removeItem("plantType"); 
    }, []);
    const selectedRouteId = localStorage.getItem("selectedRouteId");

    const selectedRoute = useMemo(() => {
      return updatedRoutes.find(route => route.id === selectedRouteId);
    }, [updatedRoutes, selectedRouteId]);
    
    const mcoService = new MCOService();
    const params = useParams();
    const instance = localStorage.getItem('instance');
    const handleChange = (event, newValue) => {
        setTapvalue(newValue);
    };

    const collectMCOdetails = async (resData) => {        
        try {
            if (resData.length) {
                return true;
            }
            const validFieldArr = resData?.mcoaffectedItem && (resData?.mcoaffectedItem?.map((vitem) => {
                const checkAttValid = !Object.keys(vitem?.materialType?.attributeMap || {}).length;
                return {
                    id: vitem.id,
                    materialTypeError: !vitem?.materialType?.name,
                    attributeFormError: checkAttValid,
                };
            }) || []);
            setItemValidationArr(validFieldArr);

            const getMasterDetails = [];
            if (resData?.mcoPropeties?.organizationId?.includes('SAP')) {
                if (resData?.mcoaffectedItem) {
                    resData?.mcoaffectedItem.forEach((vitem) => {
                        if (!getMasterDetails.length && vitem?.relatedMCOs && Object.keys(vitem?.relatedMCOs[0]).includes('id') && vitem?.relatedMCOs[0]?.id) {
                            let getMasterpayloadData = {
                                id: vitem?.relatedMCOs[0]?.id, // property id
                            }
                            getMasterDetails.push(getMasterpayloadData);
                        }
                    });
                }
                // master check
                if (getMasterDetails.length) {
                    resData['isMaster'] = (getMasterDetails[0]?.id === resData?.mcoPropeties?.id) ? "Y" : "N";
                }
            }

            if (!resData?.mcoPropeties?.hasOwnProperty("ECOExportToERP")) {
                resData.mcoPropeties.ECOExportToERP = "";
            }
            await dispatch(updateResponseData(resData));
            await dispatch(update_generalValues({
                currentMcoDetails: resData,
            }));

            await findAllMcoData(resData, getMasterDetails);
        } catch (errs) {
            handleErrorForThrows(errs, 'collectMCOdetails');
            throw errs;
        }
    }
    const findAndSaveExistingMco = async (locresData, attData, currMcoDetails, currentHaveValues) => {
        try {
            // let mcoaffectedItem = [];
            locresData.mcoaffectedItem = locresData?.mcoaffectedItem.map((gitem) => {
                const thisItem = { ...gitem };
                const getmatchItem = attData.data[0].mcoaffectedItem?.find((material) => {
                    return (gitem?.id === material.id);
                }) || {};

                const materialTypeDetails = getmatchItem?.materialType ?? null;

                thisItem['materialType'] = materialTypeDetails;

                // Oracle Fields
                const env_exportToERP = (thisItem.exportToERP?.toLowerCase() === "yes") ? true : false;
                let emr_exportToERP = getmatchItem?.exportToERP;
                if (typeof getmatchItem?.exportToERP === "string" && getmatchItem?.exportToERP?.toLowerCase() === "yes") {
                    emr_exportToERP = true;
                }
                if (typeof getmatchItem?.exportToERP === "string" && getmatchItem?.exportToERP?.toLowerCase() === "no") {
                    emr_exportToERP = false;
                }
                thisItem.makeOrBuy = (getmatchItem?.makeOrBuy) ? getmatchItem?.makeOrBuy?.toLowerCase() : thisItem?.makeOrBuy?.toLowerCase();
                thisItem.exportToERP = (getmatchItem?.exportToERP) ? emr_exportToERP : env_exportToERP;
                thisItem.itemStatus = (getmatchItem?.itemStatus) ? getmatchItem?.itemStatus?.toLowerCase() : thisItem?.ErpStatus?.toLowerCase();

                thisItem.isTemplateValueChange = false;
                thisItem.isMakeOrBuyValueChange = false;
                thisItem.isExportToERPValueChange = false;
                thisItem.isItemStatusValueChange = false;

                if (materialTypeDetails) {
                    currentHaveValues = true;
                }

                // mcoaffectedItem.push(thisItem);

                return thisItem;
            });

            if (!currMcoDetails?.mcoProperties?.hasOwnProperty("ECOExportToERP") || currMcoDetails?.mcoProperties?.ECOExportToERP === "") {
                locresData.mcoPropeties.ECOExportToERP = "yes";
            } else {
                locresData.mcoPropeties.ECOExportToERP = currMcoDetails?.mcoProperties?.ECOExportToERP;
            }
            await dispatch(updateMcoDetails({
                mcoPropeties: locresData?.mcoPropeties,
                mcoaffectedItem: locresData?.mcoaffectedItem,
            }));
            return currentHaveValues;
        } catch (err) {
            handleErrorForThrows(err, 'findAndSaveExistingMco');
            throw err;
        }
    }
    const findAndSaveInitialMco = async (locresData, currMcoDetails) => {
        try {
            locresData.mcoaffectedItem = locresData?.mcoaffectedItem.map((gitem, gkey) => {
                const thisItem = { ...gitem };
                // Oracle Fields
                // const env_exportToERP = (thisItem?.exportToERP?.toLowerCase() === "yes") ? true : false;
                let env_exportToERP = thisItem?.exportToERP;
                if (typeof thisItem?.exportToERP === "string" && thisItem?.exportToERP?.toLowerCase() === "yes") {
                    env_exportToERP = true;
                }
                if (typeof thisItem?.exportToERP === "string" && thisItem?.exportToERP?.toLowerCase() === "no") {
                    env_exportToERP = false;
                }
                thisItem.makeOrBuy = thisItem?.makeOrBuy?.toLowerCase() ?? 'buy';
                thisItem.exportToERP = env_exportToERP;
                thisItem.itemStatus = thisItem?.ErpStatus?.toLowerCase() ?? 'active';

                thisItem.isTemplateValueChange = false;
                thisItem.isMakeOrBuyValueChange = false;
                thisItem.isExportToERPValueChange = false;
                thisItem.isItemStatusValueChange = false;
                return thisItem;
            });

            if (!currMcoDetails || (!Object.keys(currMcoDetails)?.length || !currMcoDetails?.mcoProperties?.hasOwnProperty("ECOExportToERP"))) {
                locresData.mcoPropeties.ECOExportToERP = "yes";
            } else {
                locresData.mcoPropeties.ECOExportToERP = currMcoDetails?.mcoProperties?.ECOExportToERP;
            }
            await dispatch(updateMcoDetails({
                mcoPropeties: locresData.mcoPropeties,
                mcoaffectedItem: locresData.mcoaffectedItem,
            }));

            await dispatch(update_generalValues({
                currentMcoDetails: locresData,
            }));

            return locresData;
        } catch (err) {
            handleErrorForThrows(err, 'findAndSaveInitialMco');
            throw err
        }
    }
    const findAndSaveMasterMco = async (locresData, currMcoDetails, getMasterDetails, currentHaveValues, materialDataArr) => {
        try {
            if (getMasterDetails.length) {
                await mcoService.getattributesbyid(getMasterDetails).then(async (masterAttData) => {
                    await handleMasterAttData(masterAttData, getMasterDetails, locresData, currMcoDetails, currentHaveValues, materialDataArr)
                }).catch((error) => {
                    handleError(locresData, materialDataArr);
                });
            }
            return getMasterDetails;
        } catch (err) {
            handleErrorForThrows(err, 'findAndSaveMasterMco');
            throw err;
        }
    }
    const findAllMcoData = async (resData, getMasterDetails) => {
        try {
            let materialDataArr = [];
            await collectSourcePlantTemplate(resData, getMasterDetails).then(async () => {
                let getpayloadData = [{
                    id: resData?.mcoPropeties?.id, // property id
                    instance: instance
                }]
                let locresData = resData;
                await mcoService.getattributesbyid(getpayloadData).then(async (attData) => {
                    let currentHaveValues = false;
                    let currMcoDetails = {};
                    if (attData?.data[0]) {
                        currMcoDetails = attData?.data[0];

                        dispatch(update_generalValues({
                            currentMcoDetails: currMcoDetails,
                        }));

                        const getcurrentHaveValues = await findAndSaveExistingMco(locresData, attData, currMcoDetails, currentHaveValues);
                        if (typeof getcurrentHaveValues === "boolean") {
                            currentHaveValues = getcurrentHaveValues;
                        }
                    } else {
                        await findAndSaveInitialMco(locresData);
                    }
                    await findAndSaveMasterMco(locresData, currMcoDetails, getMasterDetails, currentHaveValues, materialDataArr)
                });
                // find ERP status and update 
                await setupERPStatus(locresData);
            })
        } catch (err) {
            return err
        }
    }
    const handleMasterAttData = async (masterAttData, getMasterDetails, locresData, currMcoDetails, currentHaveValues, materialDataArr) => {
        const masterData = masterAttData?.data[0] || {};
        const findMatchItem = getMatchItemFunc(getMasterDetails[0], locresData?.mcoPropeties);

        masterData.plantName = locresData?.mcoPropeties?.plantName;
        masterData.master = findMatchItem ? "Y" : "N";

        setMasterData(masterData);
        await dispatch(update_generalValues({ masterData: masterData }));

        if (shouldSetLifeCycleStep(currMcoDetails, locresData)) {
            setLifeCycleStep('Item');
        }

        let mcoaffectedItem = locresData?.mcoaffectedItem || [];
        if (!currentHaveValues) {
            mcoaffectedItem = mapMcoAffectedItem(locresData?.mcoaffectedItem, masterData.mcoaffectedItem);
            if (!masterData?.mcoaffectedItem?.length) {
                // auto saving for master mco 
                await defaultValueUpdates(locresData, materialDataArr);
            }
        }

        // Clone RME and RSME value from master mco
        const newMcoProperties = CloneDataFromMaster(locresData, masterData);
        await dispatch(updateMcoDetails({
            mcoProperties: newMcoProperties,
            mcoaffectedItem,
            masterData,
            isMaster: findMatchItem ? "Y" : "N"
        }));
    }

    const handleError = (locresData, materialDataArr) => {
        setMasterData({});
        dispatch(update_generalValues({ masterData: {} }));
        // auto saving for master mco 
        defaultValueUpdates(locresData, materialDataArr);
    }

    const getMatchItemFunc = (detail, properties) => {
        return detail?.id === properties?.id;
    }

    const shouldSetLifeCycleStep = (currMcoDetails, locresData) => {
        return currMcoDetails?.clientReleaseDate && currMcoDetails?.clientReleaseDate !== '' && ['Review'].includes(locresData?.mcoProperties?.mcostate);
    }

    const mapMcoAffectedItem = (affectedItems, masterAffectedItems) => {
        return affectedItems.map((gitem) => {
            const matchItem = masterAffectedItems?.find(material => gitem?.id === material.id) || {};
            gitem.materialType = matchItem.materialType || null;
            return gitem;
        });
    }

    const defaultValueUpdates = async (locresData, materialDataArr) => {
        locresData.mcoaffectedItem.forEach(async (affval, affkey) => {
            let findMaterialDetails = materialDataArr?.filter((foption, findex) => {
                return ((affval?.materialType && foption.templateName === affval?.materialType?.name) || (foption.desc === affval?.attributeGroupData?.ERPItemTypeTemplate));
            })
            findMaterialDetails = (findMaterialDetails?.length) ? findMaterialDetails[0] : {};
            if (Object.keys(findMaterialDetails).length && findMaterialDetails?.desc === affval?.attributeGroupData?.ERPItemTypeTemplate) {
                let aff_mcoaffectedItem = locresData.mcoaffectedItem;
                aff_mcoaffectedItem[affkey]['materialType'] = (Object.keys(findMaterialDetails).length) ? { name: findMaterialDetails?.templateName } : null;
                await updateMaterialValues(aff_mcoaffectedItem, affkey, materialDataArr, locresData);

                await dispatch(update_generalValues({
                    isMaterialFormEdited: true
                }));
            }
        })
    }

    const setupERPStatus = async (locresData) => {
        const ackspayloadData = [];
        locresData.mcoaffectedItem.forEach((mitem) => {
            ackspayloadData.push(locresData.mcoPropeties.name + '_' + mitem.name);
        });
        await mcoService.getERPStatus(ackspayloadData).then(async (acksData) => {
            if (acksData?.data) {
                const getERPStatusDetails = acksData?.data.map((eitem) => {
                    return {
                        Item_Name: eitem?.details?.Item_Name,
                        status: eitem?.details?.status,
                    };
                })

                await dispatch(updateMcoDetails({
                    itemERPStatus: [...getERPStatusDetails] // Update itemERPStatus
                }))
            }
        }).catch((errs) => {
            setIsLoading(false);
            return errs;
        })
    }

    // const isPlantExistingChecks = (titem, selectedRowData) => {
    //     return titem.plants.some(fitem => fitem.plantName === selectedRowData?.relatedMCOs[0]?.plantName);
    // }
    const mergeAttributes = (selectedRowData, materialData, templateList, propertiesData) => {
        let mergedObj = [];
        materialData?.forEach(material => {
            if (selectedRowData?.materialType?.name === material.templateName) {
                // let getMasterTempArr = templateList?.filter(titem => {
                //     if (titem?.plants) {
                //         return isPlantExistingChecks(titem, selectedRowData);
                //     } else {
                //         return titem.plantName === selectedRowData?.relatedMCOs[0]?.plantName;
                //     }
                // }) || [];

                // if (!getMasterTempArr.length) {
                //     getMasterTempArr = templateList?.filter(titem => titem.plantName === selectedRowData?.relatedMCOs[0]?.plantName) || [];
                // }

                // if (getMasterTempArr.length) {
                // getMasterTempArr.forEach(newmaterial => {
                templateList.forEach(newmaterial => {
                    if (selectedRowData?.materialType?.name === newmaterial.templateName) {
                        const masterAttrObj = newmaterial?.attributes || [];
                        const childAttrObj = material?.attributes || [];

                        if (masterAttrObj && childAttrObj) {
                            mergedObj = masterAttrObj;
                            // if (selectedRowData?.relatedMCOs[0]?.id !== propertiesData?.id) {
                            //     mergedObj = [...masterAttrObj, ...childAttrObj]
                            // }
                            mergedObj = selectedRowData?.relatedMCOs[0]?.id === propertiesData?.id
                                ? masterAttrObj
                                : masterAttrObj.concat(childAttrObj);
                        }

                        // remove duplicate if having
                        if (mergedObj?.length) {
                            const mergedArr = [...mergedObj];
                            let uniqueArray = mergedArr.filter((item, index, self) => {
                                return index === self?.findIndex((t) => (
                                    t.name === item.name
                                ))
                            });
                            mergedObj = uniqueArray;
                        }

                        dispatch(update_generalValues({ selectedRowFormdata: mergedObj }));
                    }
                });
                // }
            }
        });
        return mergedObj;
    };

    const updateFormData = async (selectedRowData, materialData, actType, propertiesData, affectedPartsData) => {
        const findSelectedRowIndex = affectedPartsData?.findIndex(aitem => selectedRowData?.id === aitem?.id);

        if (findSelectedRowIndex !== -1) {
            await dispatch(update_generalValues({ selectedRowIndex: findSelectedRowIndex }));
        }

        const mergedObj = mergeAttributes(selectedRowData, materialData, templateList, propertiesData);

        if (actType === 'form') {
            await dispatch(update_generalValues({ selectedRowData }));
        }

        // meterialFormdata(findSelectedRowIndex, mergedObj, affectedPartsData);
    };

    const handleIconClick = async (selectedRowData, materialData, actType, responseData) => {       
        const mcoData = responseData.mcoPropeties;
        const MCOPlantName = mcoData?.plantName;
        const partBU = selectedRowData?.organization;
        const plantPair = getRDOOUOrgSetMappings(templateMappingsInfo, partBU, MCOPlantName);
        if (!plantPair?.includes(MCOPlantName)) {
            if ((!Object.keys(selectedRowData).length || !selectedRowData?.materialType) || (selectedRowData === 0)) {
                setOpenAlert(true);
                setOpenDesc({
                    severity: "info",
                    desc: "Please select Material Type",
                });
                return; // Exit the function if materialType is not selected
            }
        }

        const affectedPartsData = await getAffectedPartsData();
        if (responseData?.mcoPropeties?.organizationId?.includes('SAP')) {
            if (masterData && Object.keys(masterData)?.length === 0) {
                const findmatchitem = affectedPartsData?.filter((aitem) => {
                    return aitem?.relatedMCOs[0]?.id !== responseData?.mcoPropeties?.id;
                }) || [];

                if (findmatchitem.length) {
                    setOpenAlert(true);
                    setOpenDesc({
                        severity: "info",
                        desc: "Please check master MCO attributes!",
                    });

                    return; // Exit the function if master does not have values
                }
            }
        }
        if (selectedRowData?.materialType) {
            await updateFormData(selectedRowData, materialData, actType, responseData?.mcoProperties, affectedPartsData);
        } else {
            const findSelectedRowIndex = affectedPartsData?.findIndex(aitem => selectedRowData?.id === aitem?.id);
            const mcoaffectedItem = [...affectedPartsData];
            mcoaffectedItem[findSelectedRowIndex]['materialType'] = null;
            await dispatch(updateMcoDetails(prevMcoDetails => (
                {
                    ...prevMcoDetails,
                    ...mcoaffectedItem // Update mcoaffectedItem
                }
            )))
        }

        await dispatch(update_generalValues({ showMaterialForm: true }));
    };

    const meterialFormdata = (findselectedRowIndex, mergedObj, affectedPartsData) => {
        const formData = mergedObj;
        let formInitaialValues = {};
        Object.keys(formData).length && formData?.forEach((item, index) => {
            const thisFieldName = item?.name;
            if (item?.defaultValues !== "") {
                formInitaialValues[thisFieldName] = item?.defaultValues;
            } else if (item?.defaultValues === "" && item?.inputType === "select") {
                formInitaialValues[thisFieldName] = item?.values;
            } else {
                formInitaialValues[thisFieldName] = '';
            }
            return true;
        });


        const mcoaffectedItem = [...affectedPartsData];
        if (mcoaffectedItem[findselectedRowIndex]?.materialType
            && Object.keys(mcoaffectedItem[findselectedRowIndex]?.materialType).includes("attributeMap")
        ) {
            mcoaffectedItem[findselectedRowIndex]['materialType'] = {
                name: mcoaffectedItem[findselectedRowIndex]['materialType'].name,
                attributeMap: formInitaialValues
            };
            // dispatch(updateMcoDetails(prevMcoDetails => (
            //     {
            //         ...prevMcoDetails,
            //         ...mcoaffectedItem // Update mcoaffectedItem
            //     }
            // )));

            dispatch(update_generalValues({
                affectedPartsData: mcoaffectedItem,
            }));
        }

    }

    const updateMaterialValues = async (mcoaffectedItem, findselectedRowIndex, materialDataArr, locresData) => {
        // for auto save the default values
        let findMinfo = mcoaffectedItem.filter((partsitem, partskey) => {
            return partskey === findselectedRowIndex;
        })

        findMinfo = (findMinfo.length) ? findMinfo[0] : null;
        await dispatch(updateMcoDetails({
            mcoaffectedItem: mcoaffectedItem
        }));

        await dispatch(update_generalValues({
            isMaterialFormEdited: true
        }));

        handleIconClick(findMinfo, materialDataArr, 'auto', locresData);
    };

    const submitAffectedParts = async (thistype) => {
        try {
            const affectedPartsData = await getAffectedPartsData();
            await dispatch(updateMcoDetails({
                mcoaffectedItem: affectedPartsData,
            }));
            // if (thistype === 'form') {
            //     const { valid: isValid, errDesc: errorDescription } = await validationChecks(thistype, responseData, affectedPartsData, itemValidationArr);
            //     console.log("errorDescription===",errorDescription)
            //     if (!isValid) {
            //         handleValidationFailure(errorDescription);
            //         return false    
            //     };
            // }

            const mcoProperties = responseData?.mcoPropeties;
            const plantName = mcoProperties?.plantName;
            const namePlant = `${mcoProperties?.name}_${plantName}`;

            if (thistype === 'form') setIsLoading(true);
            const resData = await mcoService.getattributesbyid([{ id: mcoProperties?.id, instance: instance }]);
            const { status: getAttrApiStatus, message: getAttrApiMsg } = resData;
            if (getAttrApiStatus === "failed") {
                setOpenAlert(true);
                setOpenDesc({
                    severity: "error",
                    desc: getAttrApiMsg,
                });
                return false;
            }

            const cloneresponseData = affectedPartsData
                ? JSON.parse(JSON.stringify(affectedPartsData))
                : [];

            const filterFieldsForUpdate = cloneresponseData?.map(fitem => {
                delete fitem?.isTemplateValueChange;
                delete fitem?.isMakeOrBuyValueChange;
                delete fitem?.isExportToERPValueChange;
                delete fitem?.isItemStatusValueChange;
                return fitem
            }) || [];
            const _payload = await prepareMRPayload();
            const payloadData = {
                id: mcoProperties?.id,
                producthub: `${namePlant}`,
                uniquePartId: `${namePlant}`,
                mcoProperties: mcoProperties,
                mcoaffectedItem: filterFieldsForUpdate,
                source: responseData?.source,
                target: responseData?.target,
                partlistPayload: _payload
            };
            if (Object.keys(resData?.data).length && resData?.data[0]) {
                const updatedData = await mcoService.updatePartListAndAttributes(payloadData);
                if (updatedData.status === 'failed') {
                    setOpenAlert(true);
                    setOpenDesc({
                        severity: "error",
                        desc: `Something went wrong while updating MR data to Enovia. Please update the templates again.`,
                    });
                    return;
                } else {
                    if (typeof updatedData === 'object') {
                        updatedData.data.mcoaffectedItem = affectedPartsData;
                        await handleResponse(updatedData, 'updated', thistype);
                        await dispatch(update_generalValues({
                            bulkPartTemplates: []
                        }));
                    }
                }                
            } else {
                const updatedData = await mcoService.updatePartListAndAddAttributes(payloadData);
                if (updatedData.status === 'failed') {
                    setOpenAlert(true);
                    setOpenDesc({
                        severity: "error",
                        desc: `Something went wrong while updating MR data to Enovia. Please update the templates again.`,
                    });
                    return;
                } else {
                    if (typeof updatedData === 'object') {
                        await handleResponse(updatedData, 'added', thistype);
                        await dispatch(update_generalValues({
                            bulkPartTemplates: []
                        }));
                    }
                }
            }
            if (thistype === 'form') {
                setTimeout(() => {
                    setIsLoading(false);
                }, 500);
            }
            return true;
        } catch (err) {
            return false
        }
    };

    const handleMRDetails = async (responseData, isload = null) => {
        try {
            const affectedPartsData = await getAffectedPartsData();
            const mrPayloadData = affectedPartsData?.map(eitem => ({
                partAndPlant: `${eitem?.name}_${responseData?.mcoPropeties?.plantName}`,
                partName: `${eitem?.name}`,
                plantName: `${responseData?.mcoPropeties?.plantName}`,
                revision: eitem?.revision,
                mcoId: responseData?.mcoPropeties?.id,
                mcoName: responseData?.mcoPropeties?.name,
                ERPStatus: eitem?.itemStatus?.toLowerCase(),
                makeOrBuy: eitem?.makeOrBuy?.toLowerCase(),
                template: eitem?.materialType?.name,
                mcobusinessUnit: responseData?.mcoPropeties?.businessUnit,
                partId: eitem?.id,
                instance: instance,
            }));

            const resData = await mcoService.getMRRelatedDetailsbyid({ mcoName: responseData?.mcoPropeties?.name });
            if (isload === "load") {
                if (!Object.keys(resData?.data || {}).length) {
                    await mcoService.addMRRelatedDetails(mrPayloadData);
                }
            } else if (Object.keys(resData?.data || {}).length && resData?.data[0]) {
                await mcoService.updateMRRelatedDetails(mrPayloadData);
            } else {
                await mcoService.addMRRelatedDetails(mrPayloadData);
            }
        } catch (error) {
            if (isload === "load") {
                handleErrorForThrows(error, 'handleMRDetails');
                throw error;
            } else {
                return error?.message;
            }
        }
    }
    const handleResponse = async (updatedData, updateType, thistype) => {
        // if (thistype === 'auto') return true;

        if (thistype === 'form') {
            setOpenAlert(true);
            setOpenDesc({
                severity: "success",
                desc: `Attributes ${updateType} successfully!`,
            });
        }
        await dispatch(update_generalValues({
            isMaterialFormEdited: false
        }));

        if (updatedData?.data) {
            const new_mcoaffectedItem = responseData.mcoaffectedItem?.map((mitem, minx) => {
                return { ...mitem, ...updatedData?.data?.mcoaffectedItem?.[minx] }
            }) || [];
            await dispatch(updateMcoDetails({
                mcoPropeties: updatedData?.data?.mcoProperties,
                mcoaffectedItem: new_mcoaffectedItem
            }));

            await handleMRDetails(responseData);
            await initialMRFunctions(responseData);
        }
    };

    const submitPropertiesForm = async (saveType = 'form') => {
        let propFormValid = validateProperties();
        if (!propFormValid && saveType !== 'auto') {
            setOpenAlert(true);
            setOpenDesc({
                severity: "error",
                desc: "Invalid Properties. Please check properties fields.",
            });
            return propFormValid;
        }

        const data = prepareFormData(responseData);

        await dispatch(update_generalValues({ isPropertyFormEdited: false }));

        if (saveType !== 'auto') {
            setIsLoading(true);
        }
        let isHavingErrors = false;
        await mcoService.updateMCOData(responseData?.mcoPropeties?.id, data).then(async (resp) => {
            if (typeof resp === 'object' && Object.keys(resp).length && !Object.keys(resp)?.includes('code')) {
                // find and update the selected rme & rsme email address
                const rmeEmailAddress = RMEList?.find((rFilter) => rFilter.key === resp?.rme) || null;
                resp["rmeEmailAddress"] = (rmeEmailAddress && Object.keys(rmeEmailAddress)?.length) ? rmeEmailAddress?.email : null;

                const rsmeEmailAddress = RSMEList?.find((rFilter) => rFilter.key === resp?.rsme) || null;
                resp["rsmeEmailAddress"] = (rsmeEmailAddress && Object.keys(rsmeEmailAddress)?.length) ? rsmeEmailAddress?.email : null;

                resp["ECOExportToERP"] = '';
                if (responseData?.mcoPropeties?.ECOExportToERP) {
                    resp["ECOExportToERP"] = responseData?.mcoPropeties?.ECOExportToERP;
                }

                await dispatch(updateMcoDetails({ mcoPropeties: resp }));

                if (Object.keys(resp)?.includes('errorMsg') && (resp?.errorMsg && resp?.errorMsg !== "")) {
                    if (saveType !== 'auto') {
                        setOpenAlert(true);
                        setOpenDesc({
                            severity: "error",
                            desc: resp?.errorMsg,
                        });

                        setIsLoading(false);
                        return false;
                    }
                    isHavingErrors = true;
                }

                if (saveType !== 'auto') {
                    setOpenAlert(true);
                    setOpenDesc({
                        severity: "success",
                        desc: "Properties updated successfully!",
                    });
                }

                // Update mcoPropeties details to database
                await updateMcoDetailsInDatabase(resp, responseData, "savePropertiesOnly");

                if (saveType !== 'auto') {
                    setIsLoading(false);
                }
            } else {
                isHavingErrors = true;
                setOpenAlert(true);
                setOpenDesc({
                    severity: "error",
                    desc: "An unexpected error occurred.",
                });
                setIsLoading(false);
            }
        }).catch(err => {
            isHavingErrors = true;
            let errorValue = null;
            if (err.message === 'Network Error') {
                errorValue = ('Network error, please check your internet connection.');
            } else if (err.code === 'ECONNABORTED') {
                errorValue = ('A timeout occurred.');
            } else if (err.response) {
                // Server responded with a status other than 2xx
                errorValue = (`Server error: ${err.response.status} ${err.response.statusText}`);
            } else {
                errorValue = ('An unexpected error occurred.');
            }

            // Additional check for net::ERR_NETWORK_CHANGED
            if (err.message.includes('net::ERR_NETWORK_CHANGED')) {
                errorValue = ('Network changed, please check your internet connection.');
            }
            if (errorValue) {
                setOpenAlert(true);
                setOpenDesc({
                    severity: "error",
                    desc: errorValue,
                });
                setIsLoading(false);
            }
        });
        return isHavingErrors;
    }
    const validateProperties = () => {
        const requiredFields = ['description', 'rme', 'rsme', 'startdate', 'ECOExportToERP'];
        for (const item in responseData?.mcoPropeties) {
            if (responseData?.mcoPropeties.hasOwnProperty(item)) {
                if (requiredFields.includes(item)) {
                    if (!responseData?.mcoPropeties[item] || responseData?.mcoPropeties[item] === "Invalid Date" || responseData?.mcoPropeties[item] === "Unassigned") {
                        return false;
                    }
                }
            }
        }
        return true;
    };

    const updateMCOState = async (status, type = null) => {
        if(instance === "SaaS"){
            if(type === 'demote' && status === 'In Approval'){
                status = 'In Work'
            } else if(status === 'In Work') {
                status = 'In Approval'
            }
        }
        try {
            if (type) setIsLoading(true);

            let itemInformation = {};
            if (status === "Release" || status === "Approved") {
                itemInformation = await prepareItemPayload(responseData);
            }

            
            let getMCOStateValidationResp = {};
            getMCOStateValidationResp = await mcoService.mcoStatusValidation([responseData?.mcoPropeties?.name]);
            if (getMCOStateValidationResp?.status === "success" && getMCOStateValidationResp?.data?.[responseData?.mcoPropeties?.name]?.status === "failed") {
                setOpenAlert(true);
                setOpenDesc({
                    severity: "error",
                    desc: getMCOStateValidationResp?.data?.[responseData?.mcoPropeties?.name]?.message,
                });
                setIsLoading(false);
                return false;
            }
            await mcoService.changeStateByMcoId(responseData?.mcoPropeties?.id, status, itemInformation).then(async (resp) => {
                if (Object.keys(resp).length) {
                    resp["ECOExportToERP"] = '';
                    if (responseData?.mcoPropeties?.ECOExportToERP) {
                        resp["ECOExportToERP"] = responseData?.mcoPropeties?.ECOExportToERP;
                    }
                    await dispatch(updateMcoDetails({
                        mcoPropeties: resp,
                    }))
                    setStepData(resp?.mcostate);
                    // update the properties in database
                    await updateMcoDetailsInDatabase(resp, responseData, "saveMcoState");

                    if (responseData?.mcoPropeties?.mcostate === resp?.mcostate) {
                        setOpenAlert(true);
                        setOpenDesc({
                            severity: "error",
                            desc: (resp?.errorMsg) ? (resp?.errorMsg) : ("Status update failed. Please verify the details and try again."),
                        });
                        return;
                    }

                    if (["Implemented", "Complete"].includes(resp?.mcostate)) {
                        setOpenAlert(true);
                        setOpenDesc({
                            severity: "success",
                            desc: "Successfully implemented!",
                        });
                        setStepData('Implemented');
                    }

                    // if (["Release"].includes(resp?.mcostate)) {
                    //     setLifeCycleStep('Stop');
                    //     setStepData('Release');
                    //     setOpenAlert(true);
                    //     setOpenDesc({
                    //         severity: "success",
                    //         desc: "Successfully released!",
                    //     });

                    if (["Create"].includes(resp?.mcostate)) {
                        setOpenAlert(true);
                        setOpenDesc({
                            severity: "info",
                            desc: "Demoted successfully!",
                        });
                    }
                    if (["Review"].includes(resp?.mcostate)) {
                        setOpenAlert(true);
                        setOpenDesc({
                            severity: "success",
                            desc: "Promoted successfully!",
                        });
                        setStepData('Review');
                    }
                }
            });
            if (type) setIsLoading(false);
        } catch (error) {
            setIsLoading(false);
            return error?.message;
        }
    }

    const checkEnoviaValidation = async (responseData) => {
        let valid = false;
        await mcoService.validationByMcoId(responseData?.mcoPropeties?.id).then(async (respValidation) => {
            const skipValidation = false;
            if (respValidation?.pass || skipValidation) {
                valid = true;
            } else if (respValidation?.fail?.length) {
                const collectFails = [];
                respValidation?.fail?.forEach((fitem) => {
                    if (fitem?.includes('Pass~~') || fitem?.includes('Pass~')) {
                        return true;
                    }
                    if (fitem?.includes('Fail~~')) {
                        const failStr = fitem?.replace("Fail~~", "");
                        collectFails.push(failStr);
                    } else if (fitem?.includes('Fail~')) {
                        const failStr = fitem?.replace("Fail~", "");
                        collectFails.push(failStr);
                    } else {
                        collectFails.push(fitem);
                    }
                })
                const altHtml = collectFails?.map((citem) => {
                    return (<li key={shortId.generate()}>{citem}</li>)
                });

                setOpenAlert(true);
                setOpenDesc({
                    severity: "error",
                    desc: altHtml,
                });
            }
        });
        return valid;
    }
    const handleLifeCycle = async (data, action) => {
        responseData.action = action;
        setIsLoading(true);
        const newIsMaterialFormEdited = await getIsMaterialFormEdited();
        // if (['Create', 'Review', 'Prepare', 'In Work', 'In Approval'].includes(data)) {
        if (['Create', 'Release', 'Implemented   '].includes(data)) {
            let getpayloadData = [{
                id: responseData?.mcoPropeties?.id, // property id
                instance: instance
            }];
            const mcoStartDate = new Date(responseData?.mcoPropeties?.startdate);
            if (mcoStartDate.getFullYear() >= 2049) {
                setOpenAlert(true);
                setOpenDesc({
                    severity: "error",
                    desc: "Please set MCO date, promotion cannot be processed with default date."
                });
                setIsLoading(false);
                return;
            }   
            const { status: getAttrApiStatus, message: getAttrApiMsg, data: getMCORespData } = await mcoService.getattributesbyid(getpayloadData);
            if (getAttrApiStatus === "failed") {
                setOpenAlert(true);
                setOpenDesc({
                    severity: "error",
                    desc: getAttrApiMsg,
                });
                setIsLoading(false);
                return false;
            }

            const getLatestMCOData = getMCORespData?.find(xitem => xitem?.id === responseData?.mcoPropeties?.id) || {};
            if (Object.keys(getMCORespData).length && getLatestMCOData[Object.keys(getMCORespData).length]) {
                if (responseData?.mcoPropeties?.mcostate !== getLatestMCOData?.mcoProperties?.mcostate) {
                    setOpenAlert(true);
                    setOpenDesc({
                        severity: "error",
                        desc: `Promote Request was started already for ${getLatestMCOData?.mcoProperties?.name}, use refresh to confirm completion or error details`,
                    });
                    setIsLoading(false);
                    return false;
                } else {
                    if (!isPropertyFormEdited) {
                        await dispatch(updateMcoDetails({
                            mcoPropeties: getLatestMCOData?.mcoProperties,
                            mcoaffectedItem: getLatestMCOData?.mcoaffectedItem
                        }));
                    }
                    if (!newIsMaterialFormEdited) {
                        await dispatch(updateMcoDetails({
                            mcoPropeties: getLatestMCOData?.mcoProperties,
                            mcoaffectedItem: getLatestMCOData?.mcoaffectedItem
                        }));
                    }
                }
            }
            if (isPropertyFormEdited) {
                const getRepVal = await submitPropertiesForm('auto');
                //if it is true means there is some error
                if (getRepVal) {
                    setIsLoading(false);
                    return getRepVal;
                }
            }
            if (['Create', 'Prepare', 'In Work'].includes(data) && newIsMaterialFormEdited) {
                await submitAffectedParts('auto');
            }

            if (!(await handleCreateOrReview(data))) {
                setIsLoading(false);
            }
        } else if (data === 'Release', 'Approved') {
            if (!(await handleRelease())) {
                setIsLoading(false);
            }
        }

        setIsLoading(false);
        return false;
    };

    const handleCreateOrReview = async (data) => {
        const skipThisValid = false;
        const affectedPartsData = await getAffectedPartsData();
        const { valid, errDesc } = await validationChecks('lifecycle', responseData, affectedPartsData, itemValidationArr);

        if (!valid && !skipThisValid) {
            handleValidationFailure(errDesc);
            return false;
        }

        let validationResponse = await getValidationResponse();
        const { valid: isDropDownValid, errDesc: errorDescription } = validationResponse;

        if (!isDropDownValid && !skipThisValid) {
            handleDropDownValidationFailure(errorDescription);
            return false;
        }

        if (data === 'Create' || data === 'Prepare' || data === 'In Work') {
            if (await handleCreate()) {
                setIsLoading(false);
                return false;
            }
        } else if (data === 'Review' || data === 'In Approval') {
            if (await handleReview()) {
                setIsLoading(false);
                return false;
            }
        }

        return true;
    };

    const getValidationResponse = async () => {
        let validationReponse = { valid: true, errDesc: [] };
        let callInternalValidation = true;
        if (systemSetingsDetail?.length) {
            let data = {
                affectedItemsInfo: responseData,
            };
            if (window?.ExternalClass) {
                const externalClass = new window.ExternalClass(data);
                // Call the greet method with a name
                validationReponse = await externalClass.dropdownValidation();
                callInternalValidation = false;
                if (!validationReponse.hasOwnProperty('valid')) {

                    //validationReponse = await dropdownValidation(responseData);
                }
            } else {
                //validationReponse = await dropdownValidation(responseData);
            }
        } else {
            //validationReponse = await dropdownValidation(responseData);
        }
        if (callInternalValidation) {
            if (responseData?.mcoPropeties?.organizationId?.includes('SAP')) {
                //validationReponse = await dropdownValidationSAP(responseData);
            } else {
                validationReponse = await dropdownValidation(responseData);
            }
        }
        return validationReponse;
    }

    const handleRelease = async () => {
        if (!(await checkEnoviaValidation(responseData))) {
            return false;
        }

        await updateMCOState('Implemented');
        await updateMCOState('Complete');
        return true;
    };

    const handleValidationFailure = (errDesc) => {
        let altHtml = errDesc;
        if (typeof errDesc === 'object' && !Array.isArray(errDesc) && errDesc !== null) {
            if (Object.keys(errDesc).length) {
                altHtml = Object.keys(errDesc)?.map((citem) => {
                    return (<li key={shortId.generate()}>{errDesc[citem]}</li>)
                });
            }
        } else if (Array.isArray(errDesc)) {
            altHtml = errDesc?.map((citem) => {
                return (<li key={shortId.generate()}>{citem}</li>)
            });
        } else if (typeof errDesc === 'string') {
            altHtml = errDesc;
        } else {
            return;
        }
        setOpenAlert(true);
        setOpenDesc({
            severity: "error",
            desc: altHtml,
        });
    };

    const handleDropDownValidationFailure = (errorDescription) => {
        const failedMsg = errorDescription?.map((mItem) => (
            <li key={shortId.generate()}>{mItem}</li>
        ));

        setOpenAlert(true);
        setOpenDesc({
            severity: "error",
            desc: failedMsg,
        });
    };

    const handleCreate = async () => {
        // await submitAffectedParts('auto');
        if(instance === 'onPrem'){
            if (await checkEnoviaValidation(responseData)) {
                // const _payload = await prepareMRPayload();
                // await mcoService.updateMRpartList(_payload);
                await updateMCOState('Review');
                return true;
            }
        } else {            
            await updateMCOState(responseData.mcoPropeties.mcostate);
        }
        

        return false;
    };

    const handleReview = async () => {
        if(instance === 'onPrem'){
            if (!(await checkEnoviaValidation(responseData))) {
                return false;
            }
        }       

        const currInstance = localStorage.getItem('instance');
        const payloadData = { mcolists: [responseData?.mcoPropeties?.name], instance: currInstance };
        try {
            const resAffData = await mcoService.sendMCO(payloadData);
            handleMCOServiceResponse(resAffData);
        } catch (error) {
            return false;
        }

        return true;
    };

    const prepareMRPayload = async () => {
        const affectedPartsData = await getAffectedPartsData();
        return affectedPartsData?.map((eItem) => {
            const partBusinessUnitValue = eItem?.organization;
            const optionalYesTemplateList = templateMappingsInfo?.filter((acItem) => (
                acItem.MappingType === 'orgallowedvalues' &&
                acItem.BUID?.toLowerCase() === partBusinessUnitValue?.toLowerCase() &&
                (acItem?.details?.Optional || "").toLowerCase() === "y"
            )).map((eItem) => eItem?.details?.ALLOWABLE_ORG_TEMPLATES) || [];

            const slOptionalNoValues = generalInfo?.mmiMBOM?.ComAssyOptionalNo?.includes(eItem?.materialType?.name) || false;

            const prevPartMRMakeOrBuyValue = mrDetails?.previousSeqMR[eItem?.name]?.[0]?.makeOrBuy || null;

            return {
                PartObjectId: eItem?.id,
                MCOId: responseData?.mcoPropeties?.id,
                PartName: eItem?.name,
                PartType: eItem?.type,
                PlantName: responseData?.mcoPropeties?.plantName,
                isMakeOrBuyChanged: (!!prevPartMRMakeOrBuyValue && prevPartMRMakeOrBuyValue !== eItem?.makeOrBuy),
                newMakeOrBuyValue: eItem?.makeOrBuy,
                template: eItem?.materialType?.name || "Unassigned",
                slOptionalYesValue: optionalYesTemplateList?.includes(eItem?.materialType?.name),
                slOptionalNoValues
            };
        });
    };

    const handleMCOServiceResponse = (resAffData) => {
        if (resAffData?.status === "success") {
            setLifeCycleStep('Item');
            setOpenAlert(true);
            setOpenDesc({
                severity: "info",
                desc: resAffData?.message,
            });
        } else {
            setOpenAlert(true);
            setOpenDesc({
                severity: "error",
                desc: resAffData?.message,
            });
        }
    };

    const fetchMCODetails = async () => {
        try {
            const resData = await mcoService.getMCOdetailById(params);
            if (resData && Object.keys(resData).length) {
                // life cycle state
                setStepData(resData?.mcoPropeties?.mcostate);
                // localStorage.setItem('systemName', resData?.source?.url);
                const findInstanceValue = (generalInfo?.systemSettings?.[resData?.source?.url]) ? generalInfo?.systemSettings?.[resData?.source?.url] : null;
                let data = {
                    bu: resData?.mcoPropeties?.businessUnit,
                    instance: findInstanceValue,
                    systemName: resData?.source?.url
                }
                const currInstance = localStorage.getItem('instance');
                if (!currInstance) {
                    // localStorage.setItem('bussinessUnit', resData?.mcoPropeties?.businessUnit);
                    localStorage.setItem('instance', findInstanceValue);
                }
                await dispatch(updateGlobalBUandInstance(data));

                return {
                    status: true,
                    data: resData
                }
            } else {
                dispatch(update_generalValues({
                    isPageHavingIssue: true
                }));

                return {
                    status: false,
                    data: "Something went wrong!"
                }
            }
        } catch (error) {
            dispatch(update_generalValues({
                isPageHavingIssue: true
            }));

            setIsLoading(false);
            return {
                status: false,
                data: error
            }
        }
    }

    const generateTempalateOptions = async (resData) => {
        let getTemplateOptions = {};
        resData?.mcoaffectedItem?.forEach(async (affval) => {
            let thisDropOptions = await getDropdownOptions(affval) || [];
            getTemplateOptions[affval?.name] = thisDropOptions;
        });

        return getTemplateOptions;
    }
    const initialDefaultFunctions = async () => {
        let resData = null;
        try {
            setIsLoading(true);
            await dispatch(resetValues());
            if(instance === 'onPrem')
            await fetchRSMEAndRMEDetails();
            resData = await fetchMCODetails();
            if (resData?.status) {
                await handleRefresh(false, resData?.data);
                await collectMCOdetails(resData?.data);
                await handleMRDetails(resData?.data, 'load');
                await initialMRFunctions(resData?.data);
                await prepareAffectedPartData();
            }
        } catch (error) {
            await dispatch(update_generalValues({
                isPageHavingIssue: true
            }));
            console.error("An error occurred:", error);
        } finally {
            setTimeout(async () => {
                setInitialLoad(true);
                setIsLoading(false);
            }, 1000); // Delay in milliseconds
        }
    }

    const generateTemplateOptFunc = async (initialLoad) => {
        const getTemplateOptions = await generateTempalateOptions(responseData);
        if (initialLoad) {
            await dispatch(update_generalValues({
                eachTemplateOptions: getTemplateOptions
            }));
        }
    }
    useEffect(() => {
        generateTemplateOptFunc(initialLoad);
    }, [initialLoad]);

    const handleRefreshButtonFunc = async (refMcoData, mcoResponseData = {}) => {
        setIsLoading(true);
        try {
            await handleRefresh(refMcoData, mcoResponseData);
        } catch (error) {
            return {
                status: false,
                data: error.message
            };
        } finally {
            setIsLoading(false);
        }
    }
    const handleRefresh = async (refMcoData, mcoResponseData = {}) => {
        try {
            if (params?.mco_id) {
                const mcoLifeCyclepayload = [params?.mco_id];
                const currentMCOLifeCycleDetails = await getCurrentMCOLifeCycleDetails(mcoLifeCyclepayload);

                if (Object.keys(currentMCOLifeCycleDetails)?.length) {
                    updateLifeCycleData(currentMCOLifeCycleDetails);

                    if (refMcoData) {
                        const resDataOfProperty = await mcoService.getMcoPropertyDetails(params?.mco_id);
                        if (resDataOfProperty && Object.keys(resDataOfProperty).length) {
                            const newMcoProperties = { ...mcoResponseData?.mcoPropeties, ...resDataOfProperty };
                            await dispatch(updateMcoDetails({
                                mcoPropeties: newMcoProperties,
                            }));
                            setStepData(resDataOfProperty?.mcostate);

                            // const resData = await fetchMCODetails();
                            // if (resData?.status) {
                            // let locresData = resDataOfProperty;
                            let getpayloadData = [{
                                id: resDataOfProperty?.id, // property id
                                instance: instance
                            }];

                            if (checkLifeCycleStep(currentMCOLifeCycleDetails, resDataOfProperty)) {
                                setLifeCycleStep('Item');
                            } else {
                                setLifeCycleStep('Stop');
                            }

                            await mcoService.getattributesbyid(getpayloadData).then(async (attData) => {
                                let currMcoDetails = {};
                                if (attData?.data[0]) {
                                    currMcoDetails = attData?.data[0];
                                    await dispatch(update_generalValues({
                                        currentMcoDetails: currMcoDetails,
                                        affectedPartsData: currMcoDetails?.mcoaffectedItem,
                                    }));
                                    await dispatch(updateMcoDetails({
                                        mcoPropeties: newMcoProperties,
                                        mcoaffectedItem: currMcoDetails?.mcoaffectedItem,
                                    }));
                                    // for re-calculate the revisions
                                    await prepareAffectedPartData();
                                }
                            })
                        }
                    } else {
                        if (checkLifeCycleStep(currentMCOLifeCycleDetails, mcoResponseData?.mcoPropeties)) {
                            setLifeCycleStep('Item');
                        } else {
                            setLifeCycleStep('Stop');
                        }
                    }
                }

                // find ERP status and update 
                await setupERPStatus(mcoResponseData);
            }
        } catch (error) {
            handleErrorForThrows(error, 'handleRefresh');
            throw error;
        }
    };

    const getCurrentMCOLifeCycleDetails = async (mcoLifeCyclepayload) => {
        try {
            const getMcoDetails = await mcoService.getLifeCycleStatusByMCOName(mcoLifeCyclepayload);
            return getMcoDetails?.data;
        } catch (err) {
            dispatch(update_generalValues({
                isPageHavingIssue: true
            }));
            handleErrorForThrows(err);
            throw err;
        }
    };

    const updateLifeCycleData = (currentMCOLifeCycleDetails) => {
        dispatch(update_generalValues({
            lifeCycleData: currentMCOLifeCycleDetails
        }));
    };

    const checkLifeCycleStep = (currentMCOLifeCycleDetails, mcoResponseData) => {
        const mcoId = params?.mco_id;
        const mcoPromotedStatus = currentMCOLifeCycleDetails?.[mcoId]?.promoted;
        const mcoReleaseStatus = currentMCOLifeCycleDetails?.[mcoId]?.releaseStatus;
        const mcoState = mcoResponseData?.mcostate.toLowerCase();
        return (["inprogress", "success"]?.includes(mcoPromotedStatus) && mcoState === "review") || ["failed"]?.includes(mcoReleaseStatus);
    };

    useEffect(() => {
        // Create a new script element
        let script = null;
        const scriptErrorHandling = (script) => {
            return () => {
                console.error(`Failed to load script: ${script.src}`);
                // Handle the error, such as logging or displaying a message to the user
            };
        };
        const loadScripts = async () => {
            if (systemSetingsDetail?.length) {
                let sitem = (systemSetingsDetail?.length) ? systemSetingsDetail[0] : null;
                if (sitem) {
                    if (typeof window?.ExternalClass === 'undefined') {
                        script = document.createElement('script');
                        // Set the source attribute to the JavaScript file you want to inject
                        script.src = `${window.location.origin}/resources/js/${sitem.file1}.js`;

                        // Use onload event to handle successful script loading
                        // script.onload = () => {
                        //     // console.log(`Script loaded: ${script.src}`);
                        // };;

                        // Use the error handler function, passing script as a parameter
                        script.onerror = scriptErrorHandling(script);

                        // Append the script element to the document body
                        // document.body.appendChild(script);

                        // Append the script element to the document body only if the Content-Type is JavaScript
                        fetch(script.src).then(response => {
                            if (!response.ok || !response.headers.get('content-type').includes('javascript')) {
                                throw new Error('Not a JavaScript file');
                            }
                            return response.blob();
                        }).then(() => {
                            document.body.appendChild(script);
                        }).catch(error => {
                            console.error(`Failed to load script: ${script.src}`, error);
                            // Handle the error, such as logging or displaying a message to the user
                        });
                    }
                }
            }
        };

        loadScripts();

        // Clean up function to remove the script when the component unmounts
        return () => {
            if (systemSetingsDetail?.length) {
                let sitem = (systemSetingsDetail?.length) ? systemSetingsDetail[0] : null;
                if (sitem) {
                    if (script && document.body.contains(script)) { // Check if script is a child of document body
                        document.body.removeChild(script);
                    }
                }
            }
        };
    }, [systemSetingsDetail]);

    useEffect(() => {
        initialDefaultFunctions();
    }, []);

    const [importBulkUpdateFile, setImportBulkUpdateFile] = useState(false)
    const bulkUploadPartandTemplate = () => {
        setImportBulkUpdateFile(true)
    }
    const savePartAndOracleTempInRedux = async (data) => {
        const bulkPartTemplates = data;
        const collectInvalidTemplates = { success: [], error: [], warning: [] };
        const affectedPartsData = await getAffectedPartsData();

        // parts validation
        let partNameExistList = [];
        // let emptyPartsExist = false;
        bulkPartTemplates?.forEach((btemp) => {
            // if (!Object.keys(btemp).includes("Part Name")) {
            //     emptyPartsExist = true;
            //     return;
            // }
            const getPartBulkTempValues = affectedPartsData?.find(affItem => {
                return (Object.keys(btemp).includes("Part Name") && btemp?.["Part Name"]?.trim() === affItem?.name)
            }) || {};
            if (Object.keys(getPartBulkTempValues)?.length) {
                partNameExistList.push(btemp?.["Part Name"]);
            } else {
                collectInvalidTemplates.error.push(`ERROR: Part ${btemp?.["Part Name"]} not found in MCO. Skipping`);
            }
        });

        const new_mcoaffectedItem = affectedPartsData?.map((affval, affkey) => {
            let newAffval = { ...affval };
            let thisDropOptions = eachTemplateOptions?.[affval?.name] || [];
            let getBulkTempValues = {};
            if (!partNameExistList?.includes(affval?.name)) {
                return affval;
            }
            if (bulkPartTemplates?.length) {
                const getPartBulkTempValues = bulkPartTemplates?.find(btemp => {
                    return (Object.keys(btemp).includes("Part Name") && btemp["Part Name"]?.trim() === affval?.name)
                }) || {};
                getBulkTempValues = materialData?.find((foption) => {
                    return Object.keys(getPartBulkTempValues).includes("Oracle Template") && getPartBulkTempValues["Oracle Template"]?.trim() === foption.templateName;
                }) || {};
                if (Object.keys(getBulkTempValues)?.length) {
                    const isExistsInOption = thisDropOptions?.some(eitem => eitem?.templateName === getBulkTempValues?.templateName);
                    // like the template existing in the dropdown then only it will set the value or else will throw error
                    if (isExistsInOption) {
                        if (affval?.materialType?.name === getBulkTempValues?.templateName) {
                            collectInvalidTemplates.warning.push(`WARNING: Same Oracle Template value already exists on MCO ${responseData?.mcoPropeties?.name} for ${affval?.name}.`);
                        } else {
                            // thisDropOptions.push(getBulkTempValues); // push the option to dropdown 
                            newAffval["materialType"] = (getBulkTempValues) ? { name: getBulkTempValues?.templateName } : null;
                            collectInvalidTemplates.success.push(`SUCCESS: ${getPartBulkTempValues["Oracle Template"]} is updated for ${affval?.name} to ${responseData?.mcoPropeties?.plantName}`);
                        }
                    } else if (getPartBulkTempValues["Oracle Template"]) {
                        // incompatiblity checks need to be done
                        // collectInvalidTemplates.error.push(`ERROR: ${getPartBulkTempValues["Oracle Template"]} is not valid Oracle Template for Part ${affval?.name} to Plant ${responseData?.mcoPropeties?.plantName}`);
                        collectInvalidTemplates.error.push(`ERROR: Part ${affval?.name} to plant ${responseData?.mcoPropeties?.plantName} is not valid due to Org Allowed, Part Type to Template, or EBOM disallowed or Missing.`);
                    }
                } else {
                    collectInvalidTemplates.error.push(`ERROR: ${getPartBulkTempValues["Oracle Template"]} is invalid template for ${affval?.name} to ${responseData?.mcoPropeties?.plantName}`);
                }
            } else {
                collectInvalidTemplates.error.push(`Invalid file. Please check the file.`);
            }

            // Find makeORBuy value and assign it
            if (Object.keys(getBulkTempValues)?.length) {
                let partBusinessUnitValue = affval?.organization;
                let buplantmapping_RDOPlants = templateMappingsInfo?.find((acitem) => {
                    return acitem.MappingType === 'buplantmapping' && acitem.BUID?.toLowerCase() === partBusinessUnitValue?.toLowerCase() && acitem?.details?.Entity.toLowerCase() === "bom or no bom";
                }) || {}
                let hasMbomTemplateList = templateMappingsInfo?.filter((acitem) => {
                    return acitem.MappingType === 'orgallowedvalues' && acitem.BUID?.toLowerCase() === partBusinessUnitValue?.toLowerCase() && acitem?.details?.Has_MBOM?.toLowerCase() === "y";
                }).map((eitem) => {
                    return eitem?.details?.ALLOWABLE_ORG_TEMPLATES;
                }) || [];

                let isExceptionalPlant = false;
                let hasMbomTemplate = false;
                if (Object.keys(hasMbomTemplateList).length) {
                    if (hasMbomTemplateList?.includes(getBulkTempValues?.templateName)) {
                        hasMbomTemplate = true;
                    }
                }
                if (Object.keys(buplantmapping_RDOPlants).length) {
                    const plantValue = buplantmapping_RDOPlants?.details?.PLANT;
                    const getPlantList = Object.keys(plantValue).filter((ekeys) => {
                        return plantValue[ekeys]?.toLowerCase() === "no bom"
                    });
                    if (getPlantList?.includes(responseData?.mcoPropeties?.plantName)) {
                        isExceptionalPlant = true;
                    }
                };
                if (hasMbomTemplate && !isExceptionalPlant) {
                    newAffval['makeOrBuy'] = "make";
                    newAffval['isMakeOrBuyValueChange'] = true;
                } else {
                    newAffval['makeOrBuy'] = "buy";
                    newAffval['isMakeOrBuyValueChange'] = true;
                }
            }

            return newAffval;
        });

        await dispatch(updateMcoDetails({
            mcoaffectedItem: new_mcoaffectedItem,
        }));
        await dispatch(update_generalValues({
            affectedPartsData: new_mcoaffectedItem,
            bulkPartTemplates: data,
            isMaterialFormEdited: true
        }));

        // if (emptyPartsExist) {
        //     collectInvalidTemplates.error.push(`Some of the "Part Name" cells are empty. Please verify the file.`);
        // }
        return collectInvalidTemplates;
    }
    const handleExportMCOData = async () => {
        const propertiesData = responseData?.mcoPropeties; // Extract PropertiesForm data
        const affectedPartsData = responseData?.mcoaffectedItem; // Extract AffectedPartsTable data
        exportMCOData(propertiesData, affectedPartsData).then((response) => {
            setOpenAlert(true);
            setOpenDesc({
                severity: response?.status,
                desc: response?.msg,
            });
        });
    };

    const isFirstRender = useRef(true);
    useEffect(() => {
        if (isFirstRender.current) {
            isFirstRender.current = false; // Set to false after the first render
            return; // Skip the first run
        }

        handleRefreshButtonFunc(true, responseData);
    }, [showTrackerBox]);

    return (
        <Container>
            {
                (!isPageHavingIssue) ? (
                    <>
                        <Box className="breadcrumb">
                            <Grid
                                container
                                spacing={1}
                                alignItems="center"
                            >
                                <Grid item md={6} sm={12} xs={12}>
                                    <Breadcrumb routeSegments={[{ name: responseData?.mcoPropeties?.name ? `${responseData.mcoPropeties.name} - MCO Details` : "MCO Details" }]} /></Grid>
                                <Grid item md={6} sm={12} xs={12} className="textAlignRight">
                                    <TrackerHistoryLinks responseData={responseData} setShowTrackerBox={setShowTrackerBox} setShowHistoryBox={setShowHistoryBox} />
                                </Grid>
                            </Grid>
                        </Box>
                        <SimpleCard>
                            <Grid container spacing={3} alignItems="center" className="elementPostionRelative">
                                <RefreshButtons handleRefreshButtonFunc={handleRefreshButtonFunc} responseData={responseData} />
                                {instance === "SaaS" ? (
                                    <SaaSLifeCycleStepperComponent
                                        selectedRoute={selectedRoute}
                                    />
                                    ) : (
                                    <LifeCycleStepperComponent
                                        stepData={stepData}
                                        lifeCycleStep={lifeCycleStep}
                                        handleLifeCycle={async (data) => {
                                        if (isPropertyFormEdited) {
                                            setOpenAlert(true);
                                            setOpenDesc({
                                            severity: "error",
                                            desc: "There are unsaved changes in the properties. Kindly save your edits before proceeding.",
                                            });
                                            return;
                                        }
                                        if (await getIsMaterialFormEdited()) {
                                            setOpenAlert(true);
                                            setOpenDesc({
                                            severity: "error",
                                            desc: "Saving changes is required or Reload to revert back to Prior Template values",
                                            });
                                            return;
                                        }
                                        handleLifeCycle(data);
                                        }}
                                        demoteState={updateMCOState}
                                    />
                                    )}

                            </Grid>
                            <Box sx={{ width: '100%', position: 'relative' }}>
                                {
                                    ([1].includes(tapvalue)) ? (
                                        <>
                                            <SaveButtons
                                                tapvalue={tapvalue}
                                                stepData={stepData}
                                                bulkUploadPartandTemplate={bulkUploadPartandTemplate}
                                                submitAffectedParts={() => submitAffectedParts('form')}
                                                exportMCOData={handleExportMCOData}
                                            />
                                        </>
                                    ) : (<></>)
                                }
                                {
                                    ([0].includes(tapvalue)) ? (
                                        <>
                                            <Button
                                                variant="contained"
                                                onClick={() => handleExportMCOData()}
                                                className="exportMcoDataBtn"
                                            >
                                                Export
                                            </Button>
                                            <Button variant="contained"
                                                onClick={() => {
                                                    submitPropertiesForm('form');
                                                }}
                                                className="saveMcoDataBtn"
                                                disabled={(["Release", "Implemented"].includes(stepData) || (!isPropertyFormEdited))}
                                            >
                                                Save
                                            </Button>
                                        </>
                                    ) : (<></>)
                                }
                                <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                                    <Tabs value={tapvalue} onChange={handleChange} aria-label="basic tabs example">
                                        <Tab label="Properties" {...a11yProps(0)} />
                                        <Tab label="Parts" {...a11yProps(1)} />
                                    </Tabs>
                                </Box>
                                <TabPanel value={tapvalue} index={0}>
                                    <PropertiesForm  selectedRoute={selectedRoute} plantType={plantType}  tapvalue={tapvalue} stepData={stepData} submitPropertiesForm={submitPropertiesForm} />
                                </TabPanel>
                                <TabPanel value={tapvalue} index={1}>
                                    <AffectedPartsTable
                                        // updateMaterialValues={updateMaterialValues}
                                        handleIconClick={handleIconClick}
                                    />
                                </TabPanel>
                            </Box>
                            <BulkUploadPartAndTemplatePupup
                                importBulkUpdateFile={importBulkUpdateFile}
                                setImportBulkUpdateFile={setImportBulkUpdateFile}
                                savePartAndOracleTempInRedux={savePartAndOracleTempInRedux}
                            />
                        </SimpleCard>
                    </>
                ) : (
                    <SomethingWrong />
                )
            }

            {/* MODAL BOX FOR TRACKER*/}
            <Dialog
                open={showTrackerBox}
                onClose={async () => {
                    setShowTrackerBox(false)
                }}
                scroll={'paper'}
                aria-labelledby="scroll-dialog-title"
                aria-describedby="scroll-dialog-description"
                maxWidth={'md'}
                fullWidth={true}
                className="zIndex999"
            >
                <DialogTitle id="scroll-dialog-title">
                    Tracker Details

                    <Button
                        variant="contained"
                        onClick={async () => {
                            await handleRefreshButtonFunc(true, responseData)
                        }}
                        disabled={((!lifeCycleData || !Object.keys(lifeCycleData)?.length) || responseData?.mcoPropeties?.mcostate?.toLowerCase() === "create")}
                        className="floatRight"
                    >
                        Refresh
                    </Button>
                </DialogTitle>
                <DialogContent dividers={true}>
                    <DialogContentText id="scroll-dialog-description" tabIndex={-1}>
                        <TrackerProgressBar fromPage={"mcodetails"} mcoName={params?.mco_id} />
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={async () => {
                        setShowTrackerBox(false);
                    }}>Close</Button>
                </DialogActions>
            </Dialog>

            {/* MODAL BOX FOR HISTORY*/}
            <HistoryDialogBox showHistoryBox={showHistoryBox} setShowHistoryBox={setShowHistoryBox} />

            {
                (openDesc) ? (
                    <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
                ) : (<></>)
            }
            {
                isLoading ? (
                    <div className="textAlignCenter">
                        <Loader show={isLoading} />
                    </div>
                ) : <></>
            }
        </Container >
    );
};
TabPanel.propTypes = {
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
    other: PropTypes.object,
    children: PropTypes.node
};

export default Mcodetails;
