import React, { useEffect, useState } from "react";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import shortId from 'shortid';
import { MCOService } from 'services/mcoService';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import {
    Table,
    TableBody,
    TableCell,
    TableRow,
    Box,
    styled,
} from "@mui/material";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import AlertTitle from '@mui/material/AlertTitle';
import CustomAlertBox from "app/components/CustomAlertBox";
import Loader from 'app/components/Loader';
// import download from 'downloadjs';
// import CustomSelect from "./CustomSelect";
const Alert = React.forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const StyledTable = styled(Table)(() => ({
    whiteSpace: "pre",
    "& thead": {
        "& tr": { "& th": { paddingLeft: 0, paddingRight: 0 } },
    },
    "& tbody": {
        "& tr": { "& td": { paddingLeft: 0, textTransform: "capitalize" } },
    },
}));

const VisuallyHiddenInput = styled('input')({
    clip: 'rect(0 0 0 0)',
    clipPath: 'inset(50%)',
    height: 1,
    overflow: 'hidden',
    position: 'absolute',
    bottom: 0,
    left: 0,
    whiteSpace: 'nowrap',
    width: 1,
});

const BulkUploadPartAndTemplatePupup = (props) => {
    const mcoService = new MCOService();
    const { importBulkUpdateFile, setImportBulkUpdateFile, savePartAndOracleTempInRedux } = props;
    const [isLoading, setIsLoading] = useState(false);
    const [noFileError, setNoFileError] = useState(false);
    const [importResponse, setImportResponse] = useState(false);
    const [importfileBar, setimportfileBar] = useState(false);
    const [importedFileData, setImportedFileData] = useState(null)
    const [importedFileName, setImportedFileName] = useState(null);
    const instance = localStorage.getItem('instance')
    const [openAlert, setOpenAlert] = useState(false);
    const [openDesc, setOpenDesc] = useState(null);
    const importFileData = (e) => {
        console.log('file selected');
        const file = e.target.files[0];
        if (file) {
            const fileType = file.name.split('.').pop();
            if (fileType === 'xlsx' || fileType === 'xls') {
                // if (e.target.files[0].name.split('.').pop() === 'xlsx' || e.target.files[0].name.split('.').pop() === 'xls') {
                setImportedFileData(e.target.files[0])
                setImportedFileName(e.target.files[0].name)
                setNoFileError(false)
            } else {
                setNoFileError(true)
                setImportedFileName(null)
                setImportedFileData(null)
            }
        } else {
            setNoFileError(true)
            setImportedFileName(null)
            setImportedFileData(null)
        }
    }
    const handleCloseImport = () => {
        setImportedFileData(null)
        setImportBulkUpdateFile(false);
    };
    const finalErrorTemplate = (showErrorAlert, uploadedTemplates) => {
        return (
            <div>
                <div style={{ fontWeight: "bold" }}>
                    {showErrorAlert?.success?.length} of {uploadedTemplates?.length} MR assignments successfully updated. {showErrorAlert?.error?.length + showErrorAlert?.warning?.length} failed.
                </div>
                {showErrorAlert?.error?.map((citem) => {
                    return (<div key={shortId.generate()}><b style={{ color: "red" }}>{citem}</b></div>)
                })}
                {showErrorAlert?.warning?.map((citem) => {
                    return (<div key={shortId.generate()}><b style={{ color: "#ebc034" }}>{citem}</b></div>)
                })}
            </div>
        )
    }
    const handleImportFile = () => {
        let hasError = false;
        if (importedFileData === null || importedFileData.length < 1) {
            setNoFileError(true);
            hasError = true
        }
        if (hasError) { return }

        if (importedFileData !== null) {
            //alert('file is upl7oaded')
            let postData = {
                'file': importedFileData,
                data: {
                    Instance: instance,
                }
            };

            // console.log(importSelectionValues, 'importSelectionValues');
            console.log(postData, 'postData');
            setIsLoading(true);
            mcoService.bulkUploadPartAndTemplate(postData).then(async (response) => {
                setimportfileBar(true);
                setImportedFileData(null)
                setImportedFileName(null)
                setImportBulkUpdateFile(false)

                if (response.status === 'success') {
                    setImportResponse(response)

                    const showErrorAlert = await savePartAndOracleTempInRedux(response?.data);
                    if (Object.keys(showErrorAlert)?.length) {
                        const altHtml = finalErrorTemplate(showErrorAlert, response?.data);
                        setOpenAlert(true);
                        setOpenDesc({
                            severity: "info",
                            desc: altHtml,
                        });
                    }
                } else {
                    if (response?.msg?.length) {
                        const altHtml = response?.msg?.map((citem) => {
                            return (<li key={shortId.generate()}>{citem}</li>)
                        }) || [];
                        setOpenAlert(true);
                        setOpenDesc({
                            severity: (response?.status === "failed") ? "error" : response?.status,
                            desc: altHtml,
                        });
                    }
                }
                setIsLoading(false);
            });
        }
    }
    const handleDownload = () => {
        let filelocation = 'partAndOracleTemp/sample-partAndOracleTemp.xlsx'
        mcoService.downloadSampleFile(filelocation)
    };
    return (
        <>
            <Dialog open={importBulkUpdateFile}
                PaperProps={{
                    style: {
                        backgroundColor: '#fff',
                        boxShadow: 'none',
                    },
                }}
                style={{
                    zIndex: 999, // Set your desired z-index value
                }}>
                <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
                    Bulk Upload Oracle Template
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        <Box width="100%" overflow="auto">
                            <StyledTable>
                                <TableBody>
                                    <TableRow>
                                        <TableCell align="left">
                                            Upload File (Excel File)
                                        </TableCell>
                                        <TableCell align="left">
                                            <div style={{ paddingTop: '10px' }}>
                                                <Button component="label" variant="contained" startIcon={<CloudUploadIcon />}>
                                                    Upload file
                                                    <VisuallyHiddenInput type="file" name="file" accept=".xls, .xlsx" onChange={importFileData} />
                                                </Button>
                                                {noFileError && (<p>Please Select xlsx file to Upload </p>)}
                                            </div>
                                            {importedFileName && (<p>{importedFileName}</p>)}
                                        </TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell colSpan={2}>
                                            <div style={{ paddingTop: '10px' }}>
                                                <a
                                                    // href={sampleFileUrl}
                                                    onClick={handleDownload}
                                                    download="Example-XLSX-document"
                                                    rel="noreferrer"
                                                    // target="_blank"
                                                    //style={{ color: "blue" }}
                                                    style={{ color: "blue", cursor: "pointer" }}
                                                >
                                                    Click Here
                                                </a> to download the sample Excel template for the Oracle Template Update.

                                                {/* <Button component="label" variant="contained" startIcon={<CloudDownloadIcon />} onClick={() => { download(sampleFileUrl) }}>
                                                Download sample file
                                            </Button> */}
                                                {/* </a> */}
                                            </div>
                                            <div style={{ paddingTop: '10px' }}>
                                                <b>Note:</b> Upload file must contain two columns with header labels in following order.
                                            </div>
                                            <div style={{ paddingTop: '10px' }}>
                                                1. Part Name<br />
                                                2. Oracle Template(Case sensitive)<br />
                                            </div>
                                        </TableCell>
                                    </TableRow>
                                </TableBody>
                            </StyledTable>
                        </Box>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleImportFile}>Done</Button>
                    <Button autoFocus onClick={handleCloseImport}>
                        Cancel
                    </Button>
                </DialogActions>
            </Dialog>
            {(openDesc) ? (
                <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
            ) : (<></>)}
            {
                isLoading ? (
                    <div style={{ textAlign: 'center' }}>
                        <Loader show={isLoading} />
                    </div>
                ) : <></>
            }
        </>
    )
}

export default BulkUploadPartAndTemplatePupup