import React, { useState, useEffect, useRef } from "react";
import { Table, TableBody, TableCell, TableHead, TableRow, Select, MenuItem, Paper, TableContainer } from '@mui/material';
import { Button } from '@mui/material';
import StatusColors from "./StatusColors";
import { useDispatch, useSelector, shallowEqual } from 'react-redux';
import { TableVirtuoso } from 'react-virtuoso';
// import { useCommonMCOFunctions } from "app/hooks/mcoHooks/useCommonMCOFunctions";
import MaterialAttributeDialogBox from 'app/components/McoPages/MaterialAttributeDialogBox';
import { Store } from "app/redux/Store";
import { update_generalValues, } from 'app/redux/actions/MCODetailsAction';
import { handleMaterialTypeSelect, findLabelForTooltip, findDropdownValue, sortAffData } from "app/utils/mcoUtils/utilities";
import "./affectedPartsStyle.css";

const exportToERPDropDownOpt = [
    { label: "Yes", value: true },
    { label: "No", value: false }
];
const itemStatusDropDownOpt = [
    { label: "Active", value: "active" },
    { label: "Inactive", value: "inactive" }
];
const makeOrBuyDropDownOpt = [
    { label: "Make", value: "make" },
    { label: "Buy", value: "buy" }
];

function emptyChecks(value) {
    return (value && value !== '') ? value : "--";
}

const columns = [
    { width: 50, label: 'Old\nRev', dataKey: 'oldRevision', textAlign: 'center' },
    { width: 50, label: 'New\nRev', dataKey: 'newRevision', textAlign: 'center' },
    { width: "20ch", label: 'Type', dataKey: 'type' },
    { width: "25ch", label: 'Part Name', dataKey: 'name' },
    { width: "30ch", label: 'Description', dataKey: 'description', textAlign: 'left' },
    { width: "30ch", label: 'Material Type Template', dataKey: 'materialTypeTemplate', textAlign: 'center' },
    // { width: 80, label: 'Affected\nMBOMs', dataKey: 'hasAffectedMBOM', textAlign: 'center' },
    { width: 90, label: 'Make-Buy', dataKey: 'makeOrBuy', textAlign: 'center' },
    { width: 80, label: 'Export\nStatus', dataKey: 'ErpStatus', textAlign: 'center' },
    { width: 80, label: 'Export \nTo ERP', dataKey: 'exportToERP', textAlign: 'center' },
    { width: 80, label: 'Sequence', dataKey: 'currentSequence', textAlign: 'center' },
    { width: 100, label: 'Item Status', dataKey: 'ERPStatus', textAlign: 'left' },
    // { width: 80, label: 'Policy', dataKey: 'policy', textAlign: 'center' },
    // { width: 80, label: 'State', dataKey: 'state', textAlign: 'center' },
    { width: 120, label: 'Actions', dataKey: 'actions', textAlign: 'center' },
];

// Convert columns array to widthValue object
const widthValue = {};
columns.forEach(column => {
    widthValue[column.dataKey] = column.width;
});

const MemoizedTableRow = React.memo(({ row, rowIndex, dropdownOpts, handleIconClick }) => {
    const {
        responseData,
        masterData,
        materialData,
        mrDetails,
        eachTemplateOptions,
        affectedPartsData,
        parentMCODetails,
    } = useSelector((state) => ({
        responseData: state.mcoDetails?.responseData,
        masterData: state.mcoDetails?.masterData,
        materialData: state.mcoDetails?.materialData,
        mrDetails: state.mcoDetails?.mrDetails,
        eachTemplateOptions: state.mcoDetails?.eachTemplateOptions,
        affectedPartsData: state.mcoDetails?.affectedPartsData,
        parentMCODetails: state.mcoDetails?.parentMCODetails,
    }), shallowEqual);
    const [affval, setAffval] = useState(row || {}); // Default to empty array
    // const [affkey, setAffkey] = useState(rowIndex || 0); // Default to empty array

    let findMaterialDetails = findDropdownValue(affval);

    let isdisabled = false;
    if (masterData && Object.keys(masterData).length && masterData?.plantName.includes('SAP')) {
        isdisabled = Object.keys(masterData).length && masterData?.master === 'N';
    }

    if (["Review", "Release", "Implemented", "Complete"].includes(responseData?.mcoPropeties?.mcostate)) {
        isdisabled = true;
    }
    if (Object.keys(findMaterialDetails || {}).length && findMaterialDetails?.desc === affval?.attributeGroupData?.ERPItemTypeTemplate) {
        isdisabled = true;
    }

    let ErpStatus = responseData?.itemERPStatus && responseData?.itemERPStatus.filter((efitem) => {
        return efitem?.Item_Name === affval?.name;
    });

    ErpStatus = (ErpStatus?.length) ? ErpStatus[0]?.status : 'Not yet processed!';

    const labelValue = findLabelForTooltip(findMaterialDetails);

    // find dropdown options
    // let thisDropOptions = getDropdownOptions(affval) || [];
    let thisDropOptions = eachTemplateOptions?.[affval?.name] || [];

    if (Object.keys(dropdownOpts)?.length && Object.keys(dropdownOpts)?.includes(affval?.name)) {
        thisDropOptions = dropdownOpts[affval?.name];
    }
    const templExistOrNot = thisDropOptions?.find(mtemp => {
        return mtemp?.id === findMaterialDetails.id;
    }) || {};
    if (!Object.keys(templExistOrNot)?.length && Object.keys(findMaterialDetails)?.length) {
        let prevSelectedTemplateObj = materialData?.find((foption) => {
            return findMaterialDetails?.Name === foption.templateName;
        });
        if (prevSelectedTemplateObj) {
            thisDropOptions = [...thisDropOptions, prevSelectedTemplateObj]
        }
    }

    const activeMRData = (Object.keys(mrDetails)?.length && mrDetails?.activeMR?.hasOwnProperty(affval?.name) && mrDetails?.activeMR[affval?.name]?.length) ? mrDetails?.activeMR[affval?.name][0] : {};
    // const newRevision = Object.keys(activeMRData)?.length ? `${affval.revision}${activeMRData?.additionalSequence}` : null;
    // const currentSequence = Object.keys(activeMRData)?.length ? `${activeMRData?.sequence}` : null;

    const previousSeqMRData = (Object.keys(mrDetails)?.length && mrDetails?.activeMR?.hasOwnProperty(affval?.name) && mrDetails?.previousSeqMR[affval?.name]?.length) ? mrDetails?.previousSeqMR[affval?.name][0] : {};
    // const oldRevision = Object.keys(previousSeqMRData)?.length ? `${previousSeqMRData?.revision}${previousSeqMRData?.additionalSequence}` : null;

    // getting active mr template for existing data from enovia
    const activeMRtemplExistOrNot = thisDropOptions?.find(mtemp => {
        return mtemp?.id === findMaterialDetails?.id;
    }) || {};
    if (!Object.keys(activeMRtemplExistOrNot)?.length) {
        let activeTemplateObj = materialData?.find((foption) => {
            return activeMRData?.template === foption.templateName;
        });
        if (activeTemplateObj && !affval?.isTemplateValueChange) {
            thisDropOptions = [...thisDropOptions, activeTemplateObj]
            findMaterialDetails = activeTemplateObj;
            // handleMaterialTypeSelect(findMaterialDetails?.templates, affkey, 'template');
        }
    }

    let defaultOptValue = {};
    if (Object.keys(previousSeqMRData || {})?.length) {
        let previousSeqMRTemplateObj = materialData?.find((foption, findex) => {
            return foption?.templateName === previousSeqMRData?.template;
        }) || {};
        if (Object.keys(previousSeqMRTemplateObj)?.length) {
            thisDropOptions.push(previousSeqMRTemplateObj); // push the option to dropdown 
            if (!affval?.isTemplateValueChange) {
                defaultOptValue = previousSeqMRTemplateObj; // for selecting the option
            }
        }
    }
    if (!Object.keys(findMaterialDetails || {})?.length && !affval?.isTemplateValueChange) {
        const isRevisionTempExist = thisDropOptions?.find((fcheckItem) => {
            return fcheckItem?.templateName === previousSeqMRData?.template;
        }) || {};

        if (!Object.keys(isRevisionTempExist)?.length && Object.keys(defaultOptValue)?.length) {
            findMaterialDetails = defaultOptValue;
            // handleMaterialTypeSelect(findMaterialDetails?.templates, affkey, 'template');
        } else if (Object.keys(isRevisionTempExist)?.length) {
            findMaterialDetails = isRevisionTempExist;
            // handleMaterialTypeSelect(findMaterialDetails?.templates, affkey, 'template');
        }
    }

    let getMakeBuyValue = (affval?.makeOrBuy)?.toLowerCase();
    let getExportToERPValue = affval?.exportToERP;
    let getItemStatusValue = (affval?.itemStatus)?.toLowerCase();
    if (!getMakeBuyValue && !affval?.isMakeOrBuyValueChange) {
        getMakeBuyValue = activeMRData?.makeOrBuy;
    }
    if (!getItemStatusValue && !affval?.isItemStatusValueChange) {
        getItemStatusValue = activeMRData?.ERPStatus?.toLowerCase();
        // handleMaterialTypeSelect(getItemStatusValue, affkey, 'itemStatus')
    }

    // remove duplicate if having
    if (thisDropOptions?.length) {
        const mergedArr = [...thisDropOptions];
        let uniqueArray = mergedArr.filter((value, index, self) => {
            return self.indexOf(value) === index;
        });
        thisDropOptions = uniqueArray;
    }

    let isHavingParentTemplate = false;
    if (Object.keys(parentMCODetails)?.length) {
        let findPartDetails = parentMCODetails?.mcoaffectedItem?.find(eitem => {
            return eitem?.name === row?.name;
        }) || {};
        if (Object.keys(findPartDetails || {}).length && findPartDetails?.materialType?.name?.length > 0) {
            isHavingParentTemplate = true;
        }
    }
    // for avoiding parent template check
    if (responseData?.mcoProperties?.name?.includes("MVO")) {
        isHavingParentTemplate = true;
    }

    useEffect(() => {
        const fetchData = async () => {
            let _thisRowData = row;
            if (affectedPartsData?.length) {
                _thisRowData = affectedPartsData?.find(foption => {
                    return foption.id === row?.id;
                });
            }
            setAffval(_thisRowData);
        };
        fetchData();
    }, [row]);

    return (
        <>
            <TableCell style={{ textAlign: 'center', width: widthValue?.oldRevision, paddingLeft: "20px" }}>{emptyChecks(affval?.oldRevision)}</TableCell>
            <TableCell style={{ textAlign: 'center', width: widthValue?.newRevision }}>{emptyChecks(affval?.newRevision)}</TableCell>
            <TableCell style={{ textAlign: 'left', width: widthValue?.type }}>{emptyChecks(affval?.displayType)}</TableCell>
            <TableCell style={{ textAlign: 'left', minWidth: widthValue?.name, maxWidth: "340px" }}>{emptyChecks(affval?.name)}</TableCell>
            <TableCell style={{ textAlign: 'left', width: widthValue?.description }}>{emptyChecks(affval?.description)}</TableCell>
            <TableCell style={{ textAlign: 'center', width: widthValue?.materialTypeTemplate }}>
                <span>
                    <Select
                        disabled={isdisabled}
                        className="form-control customSelectCss"
                        value={findMaterialDetails?.id || ""}
                        onChange={(e) => {
                            handleMaterialTypeSelect(e.target.value, row?.id, 'template');
                        }}
                        style={{ width: '100%' }}
                        displayEmpty // Enable displayEmpty prop
                        title={labelValue || null}
                        renderValue={(selected) => {
                            // if (!selected) {
                            //     return <>Unassigned</>; // Placeholder text
                            // }

                            // Render the selected value
                            const selectedOption = thisDropOptions?.find(
                                (option) => option.id === selected
                            );

                            return (selectedOption?.templateName) ? selectedOption.templateName : 'Unassigned';
                            // return (selectedOption?.templateName) ? selectedOption.instance + "_" + selectedOption.templateName + "_" + selectedOption.desc : '';
                        }}
                    >
                        <MenuItem value="">Unassigned</MenuItem>
                        {thisDropOptions?.map((option, index) => {
                            const listKey = "dpKey" + index;
                            return (
                                <MenuItem value={option.id} key={listKey}>
                                    {/* {option.templateName ? option.instance + "_" + option.templateName + "_" + option.desc : ''} */}
                                    {option.templateName ? option.templateName : ''}
                                </MenuItem>
                            )
                        })}
                    </Select>
                </span>
                {
                    (!thisDropOptions?.length) ? (<>
                        <br /><span style={{ color: 'red' }}>
                            {/* {"No Template assigned for this Part in Master Plant"} */}
                            {(!isHavingParentTemplate) ? "MVO is required to be set first" : "Template not available!"}
                        </span>
                    </>) : (<></>)
                }
            </TableCell>
            {/* <TableCell style={{ textAlign: 'center', width: widthValue?.affectedMBOMs }}>{emptyChecks(affval?.hasAffectedMBOM)}</TableCell> */}
            <TableCell style={{ textAlign: 'center', width: widthValue?.makeBuy }}>
                <Select
                    disabled={true}
                    className="form-control customSelectCss"
                    value={getMakeBuyValue}
                    onChange={(e) => handleMaterialTypeSelect(e.target.value, row?.id, 'makeOrBuy')}
                    style={{ width: '100%' }}
                    displayEmpty // Enable displayEmpty prop
                    // title={labelValue || null}
                    renderValue={(selected) => {
                        // Render the selected value
                        const makeOrBuyselectedOption = makeOrBuyDropDownOpt?.find(
                            (option) => option.value === selected
                        );
                        return makeOrBuyselectedOption?.label;
                    }}
                >
                    {makeOrBuyDropDownOpt?.map((option, index) => {
                        const listKey = "dpKeyMakeBuy" + index;
                        return (
                            <MenuItem value={option.value} key={listKey}>
                                {option.label}
                            </MenuItem>
                        )
                    })}
                </Select>
            </TableCell>
            <TableCell style={{ textAlign: 'center', width: widthValue?.exportStatus }}>
                <StatusColors colorCode="red" status={ErpStatus} />
            </TableCell>
            <TableCell style={{ textAlign: 'center', width: widthValue?.exportToERP }}>
                <Select
                    disabled={true}
                    className="form-control customSelectCss"
                    value={getExportToERPValue}
                    onChange={(e) => handleMaterialTypeSelect(e.target.value, row?.id, 'exportToERP')}
                    style={{ width: '100%' }}
                    displayEmpty // Enable displayEmpty prop
                    // title={labelValue || null}
                    renderValue={(selected) => {
                        // Render the selected value
                        const exportToERP_selectedOption = exportToERPDropDownOpt?.find(
                            (option) => option.value === selected
                        );
                        return exportToERP_selectedOption?.label;
                    }}
                >
                    {exportToERPDropDownOpt?.map((option, index) => {
                        const listKey = "dpKey2" + index;
                        return (
                            <MenuItem value={option.value} key={listKey}>
                                {option.label}
                            </MenuItem>
                        )
                    })}
                </Select>
            </TableCell>
            <TableCell style={{ textAlign: 'center', width: widthValue?.currentSequence }}>{emptyChecks(affval?.currentSequence)}</TableCell>
            <TableCell style={{ textAlign: 'center', width: widthValue?.itemStatus }}>
                <Select
                    disabled={true}
                    className="form-control customSelectCss"
                    value={getItemStatusValue}
                    onChange={(e) => handleMaterialTypeSelect(e.target.value, row?.id, 'itemStatus')}
                    style={{ width: '100%' }}
                    displayEmpty // Enable displayEmpty prop
                    // title={labelValue || null}
                    renderValue={(selected) => {
                        // Render the selected value
                        const itemStatus_selectedOption = itemStatusDropDownOpt?.find(
                            (option) => option.value === selected
                        );
                        return itemStatus_selectedOption?.label;
                    }}
                >
                    {itemStatusDropDownOpt?.map((option, index) => {
                        const listKey = "dpKey3" + index;
                        return (
                            <MenuItem value={option.value} key={listKey}>
                                {option.label}
                            </MenuItem>
                        )
                    })}
                </Select>
            </TableCell>

            {/* <TableCell style={{ textAlign: 'center', width: widthValue?.policy }}>{emptyChecks(affval?.policy)}</TableCell> */}
            {/* <TableCell style={{ textAlign: 'center', width: widthValue?.state }}>{emptyChecks(affval?.state)}</TableCell> */}

            {
                (responseData?.mcoPropeties?.organizationId.includes("SAP")) ? (
                    <TableCell style={{ textAlign: 'center', width: widthValue?.actions }}>
                        <Button onClick={async (e) => {
                            await handleIconClick(affval, materialData, 'form', responseData);
                        }}
                        >
                            <span role="img" aria-label="Open Form">
                                
                            </span>
                        </Button>
                    </TableCell>
                ) : (null)
            }

        </>
    );
}, (prevProps, nextProps) => {
    return prevProps.row === nextProps.row && prevProps.rowIndex === nextProps.rowIndex;
});

const AffectedPartsTable = React.memo((props) => {
    const { responseData } = useSelector((state) => ({
        responseData: state.mcoDetails?.responseData,
    }), shallowEqual);
    const [dropdownOpts, setDropdownOpts] = useState({});
    const [sortConfig, setSortConfig] = useState({ key: 'name', direction: 'asc' });
    const [sortedData, setSortedData] = useState(responseData?.mcoaffectedItem || []);
    const tableRef = useRef(null);

    const dispatch = useDispatch();
    const instance = localStorage.getItem('instance');

    // Function to call the loadExternalFunction function
    const loadExternalFunction = async () => {
        if (window?.ExternalClass) {
            // Create an instance of the ExternalClass class
            const externalClass = new window.ExternalClass({ affectedItemsInfo: responseData });
            // Call the getDropdownOptions method with a name
            const optsList = await externalClass.getDropdownOptions();
            setDropdownOpts(optsList);
        } else {
            console.error('ExternalClass class is not defined.');
        }
    };

    const handleSort = (key) => {
        setSortConfig((prevConfig) => ({
            key,
            direction: prevConfig.direction === 'asc' ? 'desc' : 'asc'
        }));
    };

    const VirtuosoTableComponents = {
        Scroller: React.forwardRef((props, ref) => (
            <TableContainer component={Paper} {...props} ref={ref} className="affTableContainer" />
        )),
        Table: (props) => (
            <Table {...props} sx={{ borderCollapse: 'separate' }} />
        ),
        TableHead,
        TableRow: ({ item: _item, ...props }) => {
            return <TableRow {...props} />
        },
        TableBody: React.forwardRef((props, ref) => <TableBody {...props} ref={ref} />),
    };

    function fixedHeaderContent() {
        return (
            <TableRow>
                {
                    columns.filter(fcolumns => {
                        return fcolumns.dataKey !== "actions" && instance === 'onPrem' ? responseData?.mcoPropeties?.organizationId.includes("ORACLE") : responseData?.mcoPropeties?.organizationId.includes("SAP") ;
                    }).map((column) => {
                        let columnLabel = column.label;
                        // if (column?.dataKey === "materialTypeTemplate" && instance === 'onPrem' ? responseData?.mcoPropeties?.organizationId.includes("ORACLE") : responseData?.mcoPropeties?.organizationId.includes("SAP")) {
                        //     columnLabel = "Oracle Template";
                        // } else 
                        if (column?.dataKey === "materialTypeTemplate" && responseData?.mcoPropeties?.organizationId.includes("SAP")) {
                            columnLabel = "Material Type Template";
                        } else if (column?.dataKey === "materialTypeTemplate") {
                            columnLabel = "Template";
                        }
                        let styleObj = { textAlign: column.textAlign, width: column.width, paddingLeft: column?.dataKey === "oldRevision" ? "20px" : "" };
                        if (column.dataKey === "name") {
                            styleObj = { textAlign: column.textAlign, width: column.width };
                        }
                        return (
                            <TableCell
                                key={column.dataKey}
                                style={styleObj}
                                sx={{
                                    backgroundColor: 'background.paper',
                                    cursor: 'pointer',
                                }}
                                onClick={() => handleSort(column.dataKey)}
                            >
                                {columnLabel}
                                {sortConfig.key === column.dataKey ? (
                                    <span>{sortConfig.direction === 'asc' ? ' ' : ' '}</span>
                                ) : null}
                            </TableCell>
                        )
                    })
                }
            </TableRow>
        );
    }

    useEffect(() => {
        setTimeout(() => {
            loadExternalFunction();
        }, 100);
    }, []);

    useEffect(() => {
        // let sortingData = responseData?.mcoaffectedItem || [];
        // if (affectedPartsData?.length) {
        //     sortingData = affectedPartsData;
        // }
        const sortedDataNew = sortAffData(sortConfig);

        setSortedData(sortedDataNew);

        dispatch(update_generalValues({
            exportAffectedPartsData: sortedDataNew,
        }));
    }, [sortConfig, responseData]);

    // const sortedData = sortAffData(responseData?.mcoaffectedItem || []);

    return (
        <div className={"partsTableSty"}>
            {
                sortedData.length ? (
                    <Paper className="height100Per">
                        <TableVirtuoso
                            ref={tableRef}
                            data={sortedData}
                            components={VirtuosoTableComponents}
                            fixedHeaderContent={fixedHeaderContent}
                            itemContent={(_index, row) => {
                                return <MemoizedTableRow
                                    row={row}
                                    rowIndex={_index}
                                    dropdownOpts={dropdownOpts}
                                    handleIconClick={props?.handleIconClick}
                                />
                            }}
                        />
                    </Paper>
                ) : (
                    <div style={{ textAlign: 'center' }}>No parts available!</div>
                )
            }
            <MaterialAttributeDialogBox />
        </div>
    );
});

export default AffectedPartsTable;