import React, { useEffect, useState, useImperativeHandle, forwardRef } from 'react';
import {
    Grid,
    TextField,
    Checkbox,
    FormControlLabel,
    Radio,
    RadioGroup,
} from "@mui/material";
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import { styled as makeStyles } from '@mui/material/styles';
import { Formik } from 'formik';
import * as Yup from 'yup';
import shortId from 'shortid';
import { useSelector, shallowEqual } from 'react-redux';
import PropTypes from 'prop-types';

const useStyles = makeStyles({
    spanRed: {
        color: '#FF3D57',
    },
});
const MaterialForm = forwardRef((props, ref) => {
    const {
        selectedRowData,
        selectedRowFormdata: formData,
        responseData,
        masterData,
        selectedRowIndex,
    } = useSelector((state) => ({
        selectedRowData: state?.mcoDetails?.selectedRowData,
        selectedRowFormdata: state?.mcoDetails?.selectedRowFormdata,
        responseData: state?.mcoDetails?.responseData,
        masterData: state?.mcoDetails?.masterData,
        selectedRowIndex: state?.mcoDetails?.selectedRowIndex,
    }), shallowEqual);

    const [formValues, setFormValues] = useState({});
    const [yubformData, setYubformData] = useState({});
    const classes = useStyles();

    const handleSubmit = (e) => {
        props?.saveMaterialDetails(e);
        return false;
    }

    useEffect(() => {
        let yupObj = {};
        let formInitaialValues = {};
        Object.keys(formData).length && formData?.forEach((item) => {
            const thisFieldName = item?.name;

            // skip mapping values
            if (Object.keys(selectedRowData).includes(item?.name) || (selectedRowData?.attributeGroupData && Object.keys(selectedRowData?.attributeGroupData).includes(item?.name))) {
                return true
            }

            if (item?.mandatory === "Y") {
                yupObj[thisFieldName] = Yup.string().required(thisFieldName + ' required!');
            }
            if (item?.defaultValues !== "") {
                formInitaialValues[thisFieldName] = item?.defaultValues;
            } else {
                formInitaialValues[thisFieldName] = '';
            }
        })
        setYubformData(yupObj);
        setFormValues(formInitaialValues);
    }, [selectedRowData, formData]);

    const partsSchema = Yup.object().shape(yubformData);

    return (
        <Formik
            initialValues={formValues}
            onSubmit={handleSubmit}
            enableReinitialize
            validationSchema={partsSchema}
            validateOnChange={false}
            validateOnBlur={false}
        >
            {
                function Showform({
                    values,
                    errors,
                    touched,
                    handleChange,
                    handleBlur,
                    handleSubmit,
                    isSubmitting,
                    setSubmitting,
                    setFieldValue,
                    validateForm,
                    isValid,
                    submitForm
                }) {
                    useEffect(() => {
                        setTimeout(async () => {
                            const collectMasterFields = [];

                            const currentFormFields = Object.keys(formData).length && formData?.map((ffitem, ffkey) => {
                                return ffitem.name;
                            })

                            if (
                                masterData?.mcoaffectedItem?.length &&
                                masterData?.mcoaffectedItem[selectedRowIndex]?.materialType &&
                                selectedRowIndex !== null &&
                                currentFormFields
                            ) {
                                Object.keys(masterData?.mcoaffectedItem[selectedRowIndex]?.materialType?.attributeMap).forEach((thisItem) => {
                                    const thisFieldName = thisItem;
                                    if (masterData?.mcoaffectedItem[selectedRowIndex]?.materialType?.attributeMap[thisItem] && !collectMasterFields.includes(thisFieldName) && currentFormFields.includes(thisFieldName)) {
                                        collectMasterFields.push(thisFieldName);

                                        // disable dynamic Mappings
                                        if (Object.keys(formData).length) {
                                            const isComing = formData?.filter((item, fkey) => {
                                                return (item?.name === thisFieldName) && (Object.keys(selectedRowData).includes(item?.name) || (selectedRowData?.attributeGroupData && Object.keys(selectedRowData?.attributeGroupData).includes(item?.name)))
                                            }) || [];

                                            if (isComing.length) {
                                                return true;
                                            }

                                            const updateCurrFieldValue = masterData?.mcoaffectedItem[selectedRowIndex]?.materialType?.attributeMap[thisItem];
                                            if (Array.isArray(updateCurrFieldValue)) {
                                                return true;
                                            }

                                            setFieldValue(thisFieldName, updateCurrFieldValue);
                                        }
                                    }
                                })
                            }

                            if (selectedRowData && Object.keys(selectedRowData).length) {
                                selectedRowData?.materialType?.attributeMap && Object.keys(selectedRowData?.materialType?.attributeMap).forEach((thisItem) => {
                                    const thisFieldName = thisItem;
                                    if (
                                        selectedRowData?.materialType?.attributeMap[thisItem]
                                        // && !collectMasterFields.includes(thisFieldName)
                                        && currentFormFields.includes(thisFieldName)
                                    ) {
                                        collectMasterFields.push(thisFieldName);

                                        // disable dynamic Mappings
                                        if (Object.keys(formData).length) {
                                            const isComing = formData?.filter((item, fkey) => {
                                                return (item?.name === thisFieldName) && (Object.keys(selectedRowData).includes(item?.name) || (selectedRowData?.attributeGroupData && Object.keys(selectedRowData?.attributeGroupData).includes(item?.name)))
                                            }) || [];

                                            if (isComing.length) {
                                                return true;
                                            }

                                            const updateCurrFieldValue = selectedRowData?.materialType?.attributeMap[thisItem]
                                            if (Array.isArray(updateCurrFieldValue)) {
                                                return true;
                                            }

                                            setFieldValue(thisFieldName, updateCurrFieldValue);
                                        }
                                    }
                                })
                            }

                        }, 500);
                    }, [setFieldValue])

                    useImperativeHandle(ref, () => ({
                        callsubmitForm() {
                            submitForm();
                            return isValid;
                        },
                    }));

                    return (
                        <form onSubmit={handleSubmit}>
                            <div>
                                {Object.keys(formData).length ? formData?.map((item, fkey) => {

                                    const thisInputName = item?.name;

                                    // skip the iteration
                                    if (responseData?.isMaster === "N" && ['Copy From'].includes(thisInputName)) {
                                        return true;
                                    }

                                    // disable dynamic Mappings
                                    if (Object.keys(selectedRowData).includes(thisInputName) || (selectedRowData?.attributeGroupData && Object.keys(selectedRowData?.attributeGroupData).includes(thisInputName))) {
                                        return true;
                                    }

                                    let isdisabled;
                                    if (masterData && Object.keys(masterData).length && masterData?.plantName.includes('SAP')) {
                                        isdisabled = Object.keys(masterData).length && masterData?.master === 'N' && item?.material_master === 'Y';

                                        // if (masterData?.master === 'N' && item?.material_master === 'N') {
                                        //     // return true;
                                        // }
                                    } else {
                                        isdisabled = false;
                                    }

                                    const currentFieldValue = values[thisInputName] || item?.defaultValues;

                                    return (
                                        <div className="attribute-section">
                                            <Grid
                                                container
                                                spacing={3}
                                                alignItems="center"
                                            >
                                                <Grid item md={4} sm={6} xs={12} className="font-medium">
                                                    <span >
                                                        {item?.name} {item?.mandatory === "Y" ? <span className={classes.spanRed}>*</span> : ''}
                                                    </span>
                                                </Grid>
                                                <Grid item md={8} sm={6} xs={12}>
                                                    {
                                                        (item?.inputType === 'text') && (
                                                            <TextField
                                                                className={"mb-4 w-full "}
                                                                color={(errors?.[thisInputName] ? ('warning') : (''))}
                                                                fullWidth
                                                                size="small"
                                                                variant="outlined"
                                                                id={thisInputName}
                                                                name={thisInputName}
                                                                value={currentFieldValue}
                                                                onChange={(e) => {
                                                                    setFieldValue(thisInputName, e.target.value);
                                                                }}
                                                                type="text"
                                                                disabled={isdisabled}
                                                                style={{ marginBottom: '10px' }}
                                                            />
                                                        )
                                                    }
                                                    {
                                                        (item?.inputType === 'select') && (
                                                            <>
                                                                <div>{values[thisInputName]}</div>
                                                                <Select
                                                                    disabled={isdisabled}
                                                                    value={currentFieldValue}
                                                                    onChange={(e) => {
                                                                        setFieldValue(thisInputName, e.target.value);
                                                                    }}
                                                                    displayEmpty // Enable displayEmpty prop
                                                                    renderValue={(selected) => {
                                                                        if (!selected) {
                                                                            return <em>--Select--</em>; // Placeholder text
                                                                        }
                                                                        // Render the selected value
                                                                        // const selectedOption = responseData?.productHierarchy.find(
                                                                        //     (option) => option?.sClassID === selected
                                                                        // );
                                                                        return item?.values.length && item?.values[0][selected];
                                                                    }}
                                                                    style={{ height: '37.13px', marginBottom: '1rem', width: "100%" }}
                                                                >
                                                                    <MenuItem id="">Select</MenuItem>
                                                                    {
                                                                        item?.values.length && Object.keys(item?.values[0]).map((vitem) => {
                                                                            return (
                                                                                <MenuItem value={vitem} key={shortId.generate()}>{item?.values[0][vitem]}</MenuItem>
                                                                            )
                                                                        })
                                                                    }
                                                                </Select>
                                                            </>
                                                        )
                                                    }
                                                    {
                                                        (item?.inputType === 'checkbox') && (
                                                            <>
                                                                {
                                                                    item?.values.map((vitem, vcinx) => {
                                                                        return (
                                                                            <div key={shortId.generate()}>
                                                                                <FormControlLabel
                                                                                    value={vitem}
                                                                                    label={vitem}
                                                                                    labelPlacement="end"
                                                                                    control={<Checkbox />}
                                                                                />
                                                                            </div>
                                                                        )
                                                                    })
                                                                }
                                                            </>
                                                        )
                                                    }
                                                    {
                                                        (item?.inputType === 'radio') && (
                                                            <RadioGroup
                                                                row
                                                                name={thisInputName}
                                                                sx={{ mb: 2 }}
                                                                value={values[thisInputName] ?? ""}
                                                                onChange={(e) => {
                                                                    setFieldValue(thisInputName, e.target.value);
                                                                }}
                                                                style={{ display: 'flex' }}
                                                            >
                                                                {
                                                                    item?.values.map((vitem, vrinx) => {
                                                                        return (
                                                                            <div key={shortId.generate()}>
                                                                                <FormControlLabel
                                                                                    value={vitem}
                                                                                    label={vitem}
                                                                                    labelPlacement="end"
                                                                                    control={<Radio color="secondary" />}
                                                                                />
                                                                            </div>
                                                                        )
                                                                    })
                                                                }
                                                            </RadioGroup>
                                                        )
                                                    }
                                                    <div style={{ textAlign: 'left', color: "red", position: 'relative', top: '-15px' }}>
                                                        {errors?.[thisInputName]}
                                                    </div>
                                                </Grid>
                                            </Grid>
                                        </div>
                                    )
                                }) : (
                                    <div>
                                        No attributes found.
                                    </div>
                                )}
                            </div>
                        </form>
                    )
                }
            }
        </Formik>
    );
});

MaterialForm.propTypes = {
    saveMaterialDetails: PropTypes.func.isRequired
};

export default MaterialForm;