import { useSelector, shallowEqual } from 'react-redux';

const useGetTemplateMappings = () => {
    const { responseData, templateMappingsInfo, ORGAllowedTemplateMappings } = useSelector((state) => ({
        responseData: state.mcoDetails.responseData,
        templateMappingsInfo: state.mcoDetails.templateMappingsInfo,
        ORGAllowedTemplateMappings: state.mcoDetails.ORGAllowedTemplateMappings,
    }), shallowEqual);
    
    const getTemplateMappingsFunc = (mapType) => {
        if (!mapType) {
            return [];
        }
        if (mapType === "ORGAllowedTemplateMappings") {
            return ORGAllowedTemplateMappings?.filter((item1) => {
                return item1.BUID === responseData?.mcoPropeties?.businessUnit;
            }) || [];
        } else {
            return templateMappingsInfo?.filter((item1) => {
                return item1.MappingType === mapType && item1.BUID === responseData?.mcoPropeties?.businessUnit;
            }) || [];
        }
    };
    const getTemplateMappingsFuncForTypeAndBU = (mapType="", businessUnit="") => {
        if (!mapType) {
            return [];
        }
        if (mapType === "ORGAllowedTemplateMappings") {
            return ORGAllowedTemplateMappings?.filter((item1) => {
                return item1.BUID === responseData?.mcoPropeties?.businessUnit;
            }) || [];
        } else {
            return templateMappingsInfo?.filter((item1) => {
                return item1.MappingType === mapType && item1.BUID === businessUnit;
            }) || [];
        }
    };
    return { getTemplateMappingsFunc, getTemplateMappingsFuncForTypeAndBU };
}

export { useGetTemplateMappings };