import { useSelector, shallowEqual } from 'react-redux';
import { useGetTemplateMappings } from './useGetTemplateMappings';
import generalInfo from "app/utils/mcoUtils/generalInfo.json";

const useGetDropdownOption = () => {
    const { getTemplateMappingsFunc, getTemplateMappingsFuncForTypeAndBU } = useGetTemplateMappings();
    const {
        responseData,
        templateList,
        // previousMcosDetails,
        mrDetails
    } = useSelector((state) => ({
        responseData: state.mcoDetails.responseData,
        templateList: state.mcoDetails.templates,
        // previousMcosDetails: state.mcoDetails.previousMcosDetails,
        mrDetails: state.mcoDetails.mrDetails,
    }), shallowEqual);

    const initialMVOChecks = () => {
        return generalInfo?.masterPlants?.includes(responseData?.mcoPropeties?.plantName);
    }
    // const getPreviousMcosDetails = (mcoId) => {
    //     return previousMcosDetails?.filter((prevItem) => {
    //         return prevItem.id === mcoId;
    //     }) || [];
    // }\
    const findCustomMCOTemplates = (templateName, ruleType = '', partInfo) => {
        let getMapTempList = [];
        if (templateName && templateName !== 'Unassigned') {
            let getMappedTemplatesList = null;
            switch (ruleType) {
                case "mvoallowedchanges":
                    const getMVOAllowedChangesDetails = getTemplateMappingsFunc('mvoallowedchanges');
                    getMappedTemplatesList = getMVOAllowedChangesDetails?.filter((acitem) => {
                        // return acitem?.details?.MVO_TEMPLATE_FROM === "EMR PTO MODEL FLOW";
                        return acitem?.details?.MVO_TEMPLATE_FROM === templateName;
                    }) || [];
                    if (getMappedTemplatesList.length) {
                        getMapTempList = getMappedTemplatesList[0]?.details?.MVO_TEMPLATES_TO?.map((tItem) => {
                            return tItem.MVO_TEMPLATE_TO
                        })
                    }
                    break;
                case "mvoTemplateCompatibility":
                    const getmvoTemplateCompatibility = getTemplateMappingsFunc('mvoTemplateCompatibility');
                    getMappedTemplatesList = getmvoTemplateCompatibility?.filter((acitem) => {
                        // return acitem?.details?.MVO_TEMPLATE_OPTION === "EMR ATO OPT CLASS FLOW";
                        return acitem?.details?.MVO_TEMPLATE_OPTION === templateName;
                    }) || [];

                    if (getMappedTemplatesList.length) {
                        getMapTempList = getMappedTemplatesList[0]?.details?.allowableOrgTemplates?.filter(fiItem => fiItem.status?.toLowerCase() === 'active')?.map((tItem) => {
                            return tItem.ALLOWABLE_ORG_TEMPLATE
                        })
                    }
                    break;
                case "setcompatibletemplates":
                    const getSetCompatibleTemplates = getTemplateMappingsFunc('setcompatibletemplates');
                    getMappedTemplatesList = getSetCompatibleTemplates?.filter((acitem) => {
                        // return acitem?.details?.MVO_TEMPLATE_OPTION === "EMR ATO OPT CLASS FLOW";
                        return acitem?.details?.TEMPLATE_ORG_EXISTING === templateName;
                    }) || [];

                    if (getMappedTemplatesList.length) {
                        getMapTempList = getMappedTemplatesList[0]?.details?.TEMPLATE_ORG_MODIFYING?.map((tItem) => {
                            return tItem.TEMPLATE_ORG_MODIFYING
                        })
                    }
                    break;
                default:
                    break;
            }
        }
        return getMapTempList;
    }

    const primaryToPurchageValidationMapping = (templateMappingsInfo = "") => {
        const purchageValidationMapping = templateMappingsInfo.find((item1) => {
            return item1.MappingType === 'buplantmapping' && item1?.details?.Entity.toUpperCase() === 'NONE OR PO ORG';
        });
        if (purchageValidationMapping && Object.keys(purchageValidationMapping).length) {
            const primaryToPurchagePlants = purchageValidationMapping.details?.PLANT;
            Object.keys(primaryToPurchagePlants).forEach(eachKey => {
                if (primaryToPurchagePlants[eachKey] === 'None' || primaryToPurchagePlants[eachKey] === '') {
                    delete primaryToPurchagePlants[eachKey];
                }
            })
            return primaryToPurchagePlants;
        } else {
            return {};
        }
    }



    const mappingRuleValidations = (ruleType, partType = '') => {
        let returnValue = null;
        let getMapTempList = [];
        switch (ruleType) {
            case "partTypeAndORGAllowed_TemplateMappings":
                const getPartTypeDetails = getTemplateMappingsFunc('parttypetotemplate');
                const getORGAllowedTemplateMappings = getTemplateMappingsFunc('ORGAllowedTemplateMappings');

                // checking parttype 
                const parttypetotemplateArr = getPartTypeDetails.filter((item1) => {
                    return item1.details.ENGG_ITEM_TYPE === partType;
                }) || [];

                const plantOrgAllowedTemplates = getORGAllowedTemplateMappings?.filter(orgAllowedTemplates => {
                    return Object.keys(orgAllowedTemplates?.details?.Plants).includes(responseData?.mcoPropeties?.plantName) && orgAllowedTemplates?.details?.Plants[responseData?.mcoPropeties?.plantName] === "Y"
                }) || [];

                let templatesMappingsArr = [];
                if (parttypetotemplateArr.length) {
                    plantOrgAllowedTemplates.forEach((item1) => {
                        const getPlantBasedList = parttypetotemplateArr[0]?.details?.allowableOrgTemplates.filter((item2) => {
                            return item2.org_allowed_template === item1?.details?.ALLOWABLE_ORG_TEMPLATES;
                        }) || [];
                        if (getPlantBasedList.length) {
                            templatesMappingsArr.push(item1);
                        }
                    });
                } else {
                    const flatenPartTypeTemplates = getPartTypeDetails?.map(partTypeTemplate => partTypeTemplate.details?.allowableOrgTemplates)?.flat();
                    const templatesForAllTypes = flatenPartTypeTemplates?.filter(eachRec => eachRec.exclusive_use?.toLowerCase() === 'yes')?.map(template => template.org_allowed_template)
                    let uniqueTemplatesForAllTypes = templatesForAllTypes.filter((item, index) => templatesForAllTypes.indexOf(item) === index);
                    templatesMappingsArr = plantOrgAllowedTemplates?.filter(eachTemplate => !uniqueTemplatesForAllTypes?.includes(eachTemplate?.details?.ALLOWABLE_ORG_TEMPLATES))
                }

                if (templatesMappingsArr.length) {
                    getMapTempList = templatesMappingsArr?.map((tItem) => {
                        return tItem?.details?.ALLOWABLE_ORG_TEMPLATES
                    });
                }

                returnValue = getMapTempList;
                break;
            case "buplantmapping":
                const getbuplantmapping = getTemplateMappingsFunc('buplantmapping');
                const getfilteredList = getbuplantmapping?.filter((acitem) => {
                    return acitem?.details?.PO_ORG === responseData?.mcoPropeties?.plantName;
                }) || [];
                returnValue = getfilteredList;
                break;
            default:
                break;
        }

        // console.log(getPartTypeDetails, 'getPartTypeDetails');
        // console.log(getORGAllowedTemplateMappings, 'getORGAllowedTemplateMappings');
        // console.log(parttypetotemplateArr, 'parttypetotemplateArr 111');
        // console.log(templatesMappingsArr, 'templatesMappingsArr 111')

        return returnValue;
    }

    const previousSequenceValidation = (partInfo) => {
        let resValue = false;
        const activeMRinfo = findMRTemplateByPart(mrDetails?.activeMR, partInfo) || {};
        const previousSeqMRinfo = findMRTemplateByPart(mrDetails?.previousSeqMR, partInfo) || {};
        if (!Object.keys(activeMRinfo)?.length || !Object.keys(previousSeqMRinfo)?.length) {
            return resValue;
        }
        if (previousSeqMRinfo && activeMRinfo?.sequence !== previousSeqMRinfo?.sequence) {
            resValue = true;
        }
        return resValue;
    }
    const findMRTemplateByPart = (mrLists = {}, partInfo = null) => {
        if (partInfo && mrLists && Object.keys(mrLists)?.includes(partInfo?.name)) {
            return mrLists[partInfo?.name][0];
        } else {
            return {};
        }
    }

    const getHighestSeqCurrentPendingOrActiveMR = (RDOPlantName, partInfo) => {
        const hscpAMRData = mrDetails?.getHighestSeqCurrentPendingOrActiveMR;
        if (hscpAMRData && Object.keys(hscpAMRData)?.length) {
            if (Object.keys(hscpAMRData).includes(partInfo.name)) {
                const partMRArrayData = hscpAMRData[partInfo.name];
                const getCurrentPartMRDetails = partMRArrayData.filter(eachMrData => eachMrData.partName === partInfo?.name);
                return getCurrentPartMRDetails.find(fitem => fitem.partAndPlant === `${partInfo.name}_${RDOPlantName}`);
            }
        }
        return {};
    }

    const groupByValues = (anyObject) => {
        if (!anyObject) {
            return new Map();
        }
        const purchagetoPrimaryMap = new Map();
        let purchageValue = '';
        Object.keys(anyObject)?.map(eachKey => {
            purchageValue = anyObject[eachKey];
            if (purchagetoPrimaryMap.has(purchageValue)) {
                const value = purchagetoPrimaryMap.get(purchageValue)
                purchagetoPrimaryMap.set(purchageValue, [...value, eachKey])
            } else {
                purchagetoPrimaryMap.set(purchageValue, [eachKey])
            }
        })
        return purchagetoPrimaryMap;
    }

    const purchageToPrimaryValidationMapping = (templateMappingsInfo = []) => {
        let primaryToPurchageValidation = primaryToPurchageValidationMapping(templateMappingsInfo);
        return groupByValues(primaryToPurchageValidation);
    }

    const getRDOOUOrgSetMappings = (templateMappingsInfo = [], businessUnit = '', plantToMatch = '') => {
        let buplantmapping_RDOChecks = (templateMappingsInfo?.find((acitem) => {
            return acitem.MappingType === 'buplantmapping' && acitem.BUID === businessUnit && acitem?.details?.Entity.toUpperCase() === 'RDO OU ORG SET';
        })?.details?.PLANT) || {};
        let matchedPlantsSet = []
        if (buplantmapping_RDOChecks && buplantmapping_RDOChecks[plantToMatch] && buplantmapping_RDOChecks[plantToMatch].toLowerCase() === 'yes') {
            const plantGroups = templateMappingsInfo.find((item1) => {
                return item1.MappingType === 'buplantmapping' && item1.BUID === businessUnit && item1?.details?.Entity.toUpperCase() === 'PLANTGROUP';
            })?.details.PLANT;
            const OUgroupMap = groupByValues(plantGroups);
            OUgroupMap.forEach((values, key) => {
                const filteredPlants = values?.filter(plant => buplantmapping_RDOChecks && buplantmapping_RDOChecks[plant] && buplantmapping_RDOChecks[plant].toLowerCase() === 'yes')
                if (filteredPlants?.includes(plantToMatch)) {
                    matchedPlantsSet = filteredPlants;
                    return matchedPlantsSet;
                }
            })
        }
        return matchedPlantsSet;
    }

    const getDropdownOptions = async (partInfo = null) => {
        if (!partInfo) {
            return [];
        }

        // if (responseData?.mcoPropeties?.mcostate?.toLowerCase() !== "create") {
        //     let thisDropOptions = [];
        //     const isexisttemplate = templateList.find(titem => titem?.templateName === partInfo?.materialType?.name);
        //     if (isexisttemplate) {
        //         thisDropOptions.push(isexisttemplate);
        //     }
        //     return thisDropOptions;
        // }

        let getMapTempList = [];

        const partType = partInfo?.displayType;
        // const partType = "ATO MODEL";
        // const partType = "Fastener";

        // - If Plant ID is MVO then fetch templates from the mapping "BU Allowed Templates" for MVO plant and 
        // fetch templates from the mapping "Part Type to Templates" mapping. 
        // If Both mappings are not empty then show the templates exist in both the mappings
        const appliedChecks = [];
        if (initialMVOChecks()) {
            if (previousSequenceValidation(partInfo)) {
                if (mrDetails?.previousSeqMR?.hasOwnProperty(partInfo?.name) && mrDetails?.previousSeqMR[partInfo?.name]?.length) {
                    appliedChecks.push('story 2');
                    const prevSeqTemplateValue = findMRTemplateByPart(mrDetails?.previousSeqMR, partInfo) || {};
                    getMapTempList = findCustomMCOTemplates(prevSeqTemplateValue?.template, "mvoallowedchanges", partInfo);
                }
            } else {
                appliedChecks.push('story 1');
                getMapTempList = mappingRuleValidations('partTypeAndORGAllowed_TemplateMappings', partType);
            }
        } else {
            // Story 4
            if (mrDetails?.getHighestSeqCurrentOrActiveMR && Object.keys(mrDetails?.getHighestSeqCurrentOrActiveMR)?.length) {
                const mrTemplateValue = findMRTemplateByPart(mrDetails?.getHighestSeqCurrentOrActiveMR, partInfo) || {};
                getMapTempList = findCustomMCOTemplates(mrTemplateValue?.template, "mvoTemplateCompatibility", partInfo);
                appliedChecks.push('story 4');
            }

            // Story 5
            const getORGAllowedTemplateMappings = getTemplateMappingsFunc('ORGAllowedTemplateMappings');
            const getFinalArr = getORGAllowedTemplateMappings?.filter((flitem) => {
                return flitem?.details?.Plants[responseData?.mcoPropeties?.plantName] === "Y" && getMapTempList.includes(flitem?.details?.ALLOWABLE_ORG_TEMPLATES);
            })?.map(mitem => {
                return mitem?.details?.ALLOWABLE_ORG_TEMPLATES
            }) || [];

            // If current plant is one of the purchageValidation plant list from BUPlantMappings
            // const buplantmappingChecks = mappingRuleValidations('buplantmapping');
            const getbuplantmapping = getTemplateMappingsFunc('buplantmapping');
            const purchagePlants = purchageToPrimaryValidationMapping(getbuplantmapping);
            if (purchagePlants && purchagePlants.has(responseData?.mcoPropeties?.plantName)) {
                // If Sequence is 1 then
                // Get templates for plant using OrgAllowedTemplates and filter with templates came from Story 4
                // Else sequence is not 1 then templates in story 4 is final list
                if (mrDetails?.isFirstSequence && (Object.keys(mrDetails?.isFirstSequence).includes(partInfo?.name) && mrDetails?.isFirstSequence[partInfo?.name])) {
                    getMapTempList = getFinalArr;

                }
            } else {
                getMapTempList = getFinalArr;

            }
            if (getFinalArr?.length) {
                appliedChecks.push('story 5');
            }

            // Story 6
            // Fetch templates based on the type from part type to Templates mapping and 
            const getPartTypeDetails = getTemplateMappingsFunc('parttypetotemplate');
            const parttypetotemplateArr = getPartTypeDetails.filter((item1) => {
                return item1.details.ENGG_ITEM_TYPE === partType;
            }) || [];

            // if these templates are not empty then filter these templates with templates came from story 5.
            if (parttypetotemplateArr?.length) {
                let getFilteredTemp = parttypetotemplateArr[0]?.details?.allowableOrgTemplates?.filter((ftitem) => {
                    return getMapTempList.includes(ftitem?.org_allowed_template);

                }) || [];

                if (getFilteredTemp?.length) {
                    getMapTempList = getFilteredTemp?.map((finalItem) => {
                        return finalItem?.org_allowed_template;
                    });
                    appliedChecks.push('story 6');
                }


            } else {
                // Story 7
                const flatenPartTypeTemplates = getPartTypeDetails?.map(partTypeTemplate => partTypeTemplate.details?.allowableOrgTemplates)?.flat();
                const templatesForAllTypes = flatenPartTypeTemplates?.filter(eachRec => eachRec.exclusive_use?.toLowerCase() === 'yes')?.map(template => template.org_allowed_template) || [];
                getMapTempList = getMapTempList?.filter(eachTemplate => !(templatesForAllTypes?.includes(eachTemplate)))
            }

            // Story 8
            //vara--> here we have to call getbuplantMapping based on part RDO
            const partRDOBasedBUMapping = getTemplateMappingsFuncForTypeAndBU('buplantmapping', partInfo.organization) //this needs correction


            const plantsPair = getRDOOUOrgSetMappings(partRDOBasedBUMapping, partInfo.organization, responseData?.mcoPropeties?.plantName);
            let templateFromHighestSequence = []
            plantsPair.forEach(RDOPlantName => {
                if (RDOPlantName !== responseData?.mcoPropeties?.plantName) {
                    //1) PartRDOPlantName, HeighestSeq, ERPStatus Active, status current|pending|prepending
                    //2) PartRDOPlantName, HeighestSeq, ERPStatus Active, status current|pending|prepending
                    //get highestSequence template for this plant and part combination
                    //if this template is not null then call RDOOUSetCompatible templates
                    let resMRData = getHighestSeqCurrentPendingOrActiveMR(RDOPlantName, partInfo);
                    if (resMRData) {
                        let getCustomMCOTempaltes = findCustomMCOTemplates(resMRData.template, "setcompatibletemplates", partInfo);
                        if (getCustomMCOTempaltes?.length)
                            templateFromHighestSequence.push(getCustomMCOTempaltes);
                    }
                }
            })
            const finalTemplatesList = getMapTempList?.filter((citem) => {
                const flattenedArray = templateFromHighestSequence.flat();
                return flattenedArray.includes(citem);
            }) || [];
            getMapTempList = templateFromHighestSequence.length ? finalTemplatesList : getMapTempList;
            appliedChecks.push('story 8');
        }

        let thisDropOptions = [];
        if (getMapTempList.length) {
            getMapTempList.forEach((ftItem) => {
                const isexisttemplate = templateList.find(titem => titem?.templateName === ftItem);
                if (isexisttemplate) {
                    thisDropOptions.push(isexisttemplate);
                }
            });
        }
        thisDropOptions.sort((a, b) => a.templateName.localeCompare(b.templateName));
        console.log(appliedChecks, "=======dropdown option appliedChecks", partInfo?.name);
        thisDropOptions = thisDropOptions.sort((a, b) => a.templateName.localeCompare(b.templateName));
        return thisDropOptions;
    }

    return getDropdownOptions;
}
export { useGetDropdownOption };