import { useEffect, useState } from "react";
import Stack from "@mui/material/Stack";
import Stepper from "@mui/material/Stepper";
import Step from "@mui/material/Step";
import Link from "@mui/material/Link";
import { Grid } from "@mui/material";
import { MCOService } from "services/mcoService";
import CustomAlertBox from "../CustomAlertBox";
import Loader from "../Loader";

const actionTextVar = {
  promote: "Promote >>",
};

export default function SaaSLifeCycleStepperComponent(props) {
  const { selectedRoute } = props;

  const [activeStep, setActiveStep] = useState("Create");
  const [enabledSteps, setEnabledSteps] = useState(["Create"]);
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  let instance = localStorage.getItem("instance");

  let steps =
    instance === "SaaS"
      ? ["Create", "Review", "Release"]
      : ["Create", "Release", "Implemented"];

  const mcoService = new MCOService();
  useEffect(() => {
    // If user already promoted to next step previously, recover that step from localStorage
    const savedStep = localStorage.getItem(
      `saasLifecycleStep_${selectedRoute?.id}`
    );
    if (savedStep && steps.includes(savedStep)) {
      setActiveStep(savedStep);
      const currentIndex = steps.indexOf(savedStep);
      setEnabledSteps(steps.slice(0, currentIndex + 1));
    }
  }, [selectedRoute?.id]);

  const handlePromote = async () => {
    if (!selectedRoute || !selectedRoute.id) return;
    setIsLoading(true);

    try {
      const currentIndex = steps.indexOf(activeStep);
       //  Only call routeStart before first promotion
       if (activeStep === 'Create') {
        const routeStartResponse = await mcoService.routeStart(selectedRoute.id);
        console.log("routeStartResponse", routeStartResponse);
  
        if (routeStartResponse?.routeStatus !== 'Started') {
          throw new Error("Route not started properly");
        }
      }
      //  Step 2: Fetch Task List
      const taskList = await mcoService.getTaskList(selectedRoute.id);
      let taskId = null;
      if (Array.isArray(taskList)) {
        //  If multiple tasks, find the first one that's not complete
        const pendingTask = taskList.find(
          (task) => task.current?.toLowerCase() !== "complete"
        );
        taskId = pendingTask?.id;
        if (!taskId && taskList.length === 1) {
          //  Fallback if it's just one and may not have 'current' field
          taskId = taskList[0].id;
        }
      } else if (typeof taskList === "string") {
        taskId = taskList;
      }
       // Step 3: Approve Task
      if (taskId) {
        const approvalResponse = await mcoService.taskApproval(taskId);
        if (approvalResponse?.success) {
          const currentIndex = steps.indexOf(activeStep);
          const nextStep = steps[currentIndex + 1];
          if (nextStep) {
            setEnabledSteps((prev) => [...new Set([...prev, nextStep])]);
            setActiveStep(nextStep);
            localStorage.setItem(
              `saasLifecycleStep_${selectedRoute?.id}`,
              nextStep
            );
          }
          setOpenAlert(true);
          setOpenDesc({
            severity: "success",
            desc: " Task successfully approved!",
          });
        }
      }
    } catch (error) {
      setOpenAlert(true);
      setOpenDesc({
        severity: "error",
        desc:
          error?.response?.data?.details?.Response?.ErrorMessage ||
          "Approval failed",
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Grid item md={12} sm={12} xs={12} className="font-medium">
      <div
        style={{
          margin: "auto auto 25px auto",
          textAlign: "center",
          width: "75%",
          position: "relative",
        }}
      >
        {openDesc && (
          <CustomAlertBox
            openAlert={openAlert}
            openDesc={openDesc}
            setOpenAlert={setOpenAlert}
          />
        )}
        {isLoading && (
          <div style={{ textAlign: "center" }}>
            <Loader show={isLoading} />
          </div>
        )}
        <Stack sx={{ width: "100%" }} spacing={4}>
          <Stepper alternativeLabel activeStep={steps.indexOf(activeStep)}>
            {steps.map((label, sindex) => (
              <Step key={label}>
                <div
                  style={{
                    width: "auto",
                    minWidth: "110px",
                    display: "inline-table",
                    padding: "10px",
                    color: activeStep === label ? "#ffffff" : "#5c5c5c",
                    background: activeStep === label ? "#42a2da" : "#e6e7e8",
                    zIndex: "1",
                    position: "relative",
                    top: "-10px",
                    borderRadius: "-10px",
                    boxShadow: "0px 2px 2px 0px rgba(0, 0, 0, 0.3)",
                  }}
                  data-state={label}
                >
                  {sindex !== 0 && (
                    <span
                      style={{
                        width: "0",
                        height: "0",
                        borderStyle: "solid",
                        borderTop: "7px solid transparent",
                        borderBottom: "7px solid transparent",
                        borderLeft: "10px solid #bdbdbd",
                        position: "absolute",
                        left: "-10px",
                        top: "15.5px",
                      }}
                    ></span>
                  )}
                  {label}
                </div>
                {(enabledSteps.includes(label) && activeStep === label && label !== 'Release') && (
                  <div>
                    <Link
                      component="button"
                      variant="body2"
                      underline="none"
                      title={actionTextVar.promote}
                      onClick={handlePromote}
                    >
                      {actionTextVar.promote}
                    </Link>
                  </div>
                )}
              </Step>
            ))}
          </Stepper>
        </Stack>
      </div>
    </Grid>
  );
}
