import React from "react";
import IconButton from '@mui/material/IconButton';
import VisibilityIcon from '@mui/icons-material/Visibility';

const StatusColors = (props) => {
    let colorCode = 'grey';
    let tooltipLabel = props?.status?.toLowerCase();

    const capitalize = s => (s && s[0].toUpperCase() + s.slice(1)) || "";

    switch (tooltipLabel) {
        case "success":
            colorCode = 'limegreen';
            break;
        case "inprogress":
            colorCode = 'orange';
            break;
        case "failed":
            colorCode = 'red';
            break;
        default:
            colorCode = 'grey';
            break;
    }

    return (
        <>
            {/* For display status by circled with colors */}
            <span title={capitalize(props?.status)}
                style={{
                    width: '10px',
                    height: '10px',
                    backgroundColor: colorCode,
                    borderRadius: '50%',
                    display: "block",
                    margin: 'auto',
                }}>
            </span>

            {/* For display status text */}
            {/* {
                (["success", "inprogress", "failed"]?.includes(props?.status)) ? capitalize(props?.status) : "--"
            } */}
            {/* For display status by icons with color differentiate */}
            {/* {
                (["success", "inprogress", "failed"]?.includes(props?.status)) ? (
                    <span title={capitalize(props?.status)}>
                        <VisibilityIcon

                            sx={(theme) => ({
                                position: 'relative',
                                right: 0,
                                top: '2.5px',
                                color: (props?.status?.toLowerCase() === "success" ? "limegreen" : "red") ?? "#1976d2",
                            })} />
                    </span>
                ) : "--"
            } */}
        </>
    )
}

export default StatusColors;