import React, { useEffect, useState } from "react";
import Box from '@mui/material/Box';
// import Button from '@mui/material/Button';
import shortId from 'shortid';
import { TextField } from "@mui/material";
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import Grid from '@mui/system/Unstable_Grid';
import Autocomplete from '@mui/material/Autocomplete';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { DateTimePicker } from '@mui/x-date-pickers/DateTimePicker';
import dayjs from 'dayjs';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import {
  updateMcoDetails,
  update_generalValues,
} from "app/redux/actions/MCODetailsAction";
import CustomAlertBox from "../McoPages/CustomAlertBox";
import CustomDateTimePickerToolbar from "./CustomDateTimePickerToolbar";
import './mcoStyle.css';
import { trimmingDisplayContent } from "app/utils/mcoUtils/utilities";

const capitalize = s => s && s[0].toUpperCase() + s.slice(1)
const instance = localStorage.getItem('instance');
const labelArr = {
  startDate: "Startdate",
  rme: instance === 'onPrem' ? "Responsible Manufacturing Engineer" : '',
  rsme: instance === 'onPrem' ? "Responsible Senior Manufacturing Engineer" : '',
  mcostate: "State",
  responseFromErp: "Export Status",
  plantName: "Plant",
  businessUnit: "Design Organization",
  description: "Description",
  ECOExportToERP: "ECO Export To ERP"
};

const PropertiesForm = (props) => {
  let { plantType, selectedRoute  } = props;
  const dispatch = useDispatch();
  const { responseData, RSMEList, RMEList, currentMcoDetails, isPropertyFormEdited } = useSelector((state) => state.mcoDetails);
  // const { tapvalue, stepData, submitPropertiesForm } = props;
  const [errorFields, setErrorFields] = useState({});
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const validateForm = (action = '') => {
    let errors = {};
    if (Object.keys(responseData?.mcoPropeties || {}).length) {
      const values = responseData?.mcoPropeties;

      // Validation logic for each field
      // Assuming values.startdate is a Date object or a string in ISO 8601 format (e.g., '2024-04-19')
      if (!values.startdate) {
        errors.startdate = `${labelArr['startDate']} is required`;
      } else {
        let currentDate = new Date();
        currentDate.setHours(0, 0, 0, 0); // Set current date's time to midnight

        let parts = values.startdate.split(/[\s/:]+/); // Split the string by space, /, and :
        let startDate = new Date(parts[2], parts[0] - 1, parts[1], parts[3], parts[4], parts[5]); // Year, month (0-based), day, hour, minute, second
        if (startDate < currentDate) {
          errors.startdate = `${labelArr['startDate']} must be a present or future date`;
        }
      }

      if ((!values.rme || values.rme?.toLowerCase() === "unassigned") && instance === 'onPrem') {
        errors.rme = `${labelArr['rme']} is required`;
      }
      if (!values.rsme || values.rsme?.toLowerCase() === "unassigned" && instance === 'onPrem') {
        errors.rsme = `${labelArr['rsme']} is required`;
      }
      if (!values.description) {
        errors.description = `${labelArr['description']} is required`;
      }
      if (!values.ECOExportToERP) {
        errors.ECOExportToERP = `${labelArr['ECOExportToERP']} is required`;
      }
    }
    setErrorFields(errors);
    if (Object.keys(errors || {}).length) {
      const altHtml = Object.keys(errors || {})?.map((citem) => {
        return (<li key={shortId.generate()}>{errors[citem]}</li>)
      });

      if (action === 'click') {
        setOpenAlert(true);
        setOpenDesc({
          severity: "error",
          desc: altHtml,
        });
      }
    }

    return errors;
  };

  useEffect(() => {
    validateForm();
  }, [responseData]);

  return (
    <>
      {Object.keys(responseData?.mcoPropeties || {}).map((key) => {
        if (
          [
            "id",
            "type",
            "physicalid",
            // "businessUnit",
            "organizationId",
            "rmeEmailAddress",
            "rsmeEmailAddress",
            "startDateDisplayValue",
            "errorMsg",
          ].includes(key) || (key === "responseFromErp" && ((!currentMcoDetails || !currentMcoDetails?.ERPstatus) || !currentMcoDetails))
        ) {
          return false;
        }

        let filterValue = null;
        if (["rme", "rsme"].includes(key) && instance === 'onPrem') {
          filterValue = ((key === "rme" ? RMEList : RSMEList) || [])?.find(
            (rmeFilter) => rmeFilter.key === responseData?.mcoPropeties[key]
          );
        }
        let ECOExportToERP = responseData?.mcoPropeties?.ECOExportToERP;
        if (key === "ECOExportToERP" && (!responseData?.mcoPropeties?.ECOExportToERP || responseData?.mcoPropeties?.ECOExportToERP === "")) {
          let mcoPropeties = responseData?.mcoPropeties;
          mcoPropeties["ECOExportToERP"] = 'yes';
          dispatch(updateMcoDetails({
            mcoPropeties: mcoPropeties,
          }));
        }

        const disableAllFields = !["Create", "Review", "Prepare", 'In Work']?.includes(responseData?.mcoPropeties?.mcostate);

        return (
          <>
            <Grid container spacing={3} alignItems="center" key={key}>
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                {
                  (["startdate"].includes(key) && (responseData?.mcoPropeties?.startDateDisplayValue && responseData?.mcoPropeties?.startDateDisplayValue !== 'null')) ? (
                    <span style={{ position: 'relative', top: "-25px" }}>
                      {Object.keys(labelArr || {}).includes(key)
                        ? capitalize(labelArr[key])
                        : capitalize(key)}
                    </span>
                  ) : (
                    <span>
                      {Object.keys(labelArr || {}).includes(key)
                        ? capitalize(labelArr[key])
                        : capitalize(key)}
                    </span>
                  )
                }
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                {
                  ["startdate"].includes(key) && (
                    <>
                      <LocalizationProvider dateAdapter={AdapterDayjs}>
                        <DateTimePicker
                          disabled={disableAllFields}
                          minDate={dayjs()} // This disables past dates
                          views={[
                            "year",
                            "month",
                            "day",
                            // "hours",
                            // "minutes",
                            // "seconds",
                          ]}
                          value={
                            responseData.mcoPropeties[key] &&
                              responseData.mcoPropeties[key] !== ""
                              ? dayjs(responseData.mcoPropeties[key])
                              : null
                          }
                          onChange={(newValue) => {
                            let mcoPropeties = responseData?.mcoPropeties;
                            if (newValue) {
                              let getTime = dayjs(newValue.$d).format(
                                "MM/DD/YYYY hh:mm:ss A"
                              );
                              if (getTime !== "Invalid Date") {
                                mcoPropeties["startdate"] = getTime ?? null;
                                dispatch(updateMcoDetails({
                                  mcoPropeties: mcoPropeties,
                                }));
                              } else if (!newValue) {
                                mcoPropeties["startdate"] = newValue;
                                dispatch(updateMcoDetails({
                                  mcoPropeties: mcoPropeties,
                                }));
                              }
                              dispatch(update_generalValues({
                                isPropertyFormEdited: true,
                              }));
                            } else if (!newValue) {
                              mcoPropeties["startdate"] = newValue;
                              dispatch(updateMcoDetails({
                                mcoPropeties: mcoPropeties,
                              }));
                            }
                          }}
                          className={`datatimeInputCls ${responseData?.mcoPropeties?.mcostate}`}
                          sx={{
                            padding: '0px!important',
                            width: '100%!important',
                            marginBottom: '1rem !important',
                            '@global': {
                              'input': {
                                padding: '8.5px 14px!important',
                                height: '0.4375em!important',
                              }
                            }
                          }}
                          slots={{
                            toolbar: CustomDateTimePickerToolbar,
                          }}
                        />
                      </LocalizationProvider>
                      {(responseData?.mcoPropeties?.startDateDisplayValue && responseData?.mcoPropeties?.startDateDisplayValue !== 'null') ? (<>
                        <div style={{ padding: "0px 15px 15px 0px" }}><b>Start Date Equivalent To:</b> {responseData?.mcoPropeties?.startDateDisplayValue}</div>
                      </>) : (null)}</>
                  )
                }
                {
                  (['rme', 'rsme'].includes(key) && instance === 'onPrem') && (
                    <Autocomplete
                      disabled={disableAllFields}
                      id={"autoComplete_" + key}
                      value={filterValue?.label || null}
                      autoHighlight
                      onChange={(e, newValue) => {
                        let mcoPropeties = responseData.mcoPropeties;
                        mcoPropeties[key] = newValue ? (newValue.key || null) : null;
                        dispatch(updateMcoDetails({
                          mcoPropeties: mcoPropeties,
                        }));
                        dispatch(update_generalValues({
                          isPropertyFormEdited: true,
                        }));
                      }}
                      onInputChange={(e, newInputValue) => {
                        //sss
                      }}
                      options={(key === 'rme') ? RMEList : RSMEList}
                      // sx={{ width: '100%', padding: '0px 0px 10px 0px' }}
                      renderInput={(params) => <TextField {...params} className={errorFields?.[key] ? 'errorAutocomplete' : ''} />}
                      renderOption={(props, option) => {
                        return (
                          <Box component="li" sx={{ '& > img': { mr: 2, flexShrink: 0 } }} {...props}>
                            {option.label}
                          </Box>
                        )
                      }}
                    />
                  )
                }
                {["responseFromErp"].includes(key) && currentMcoDetails && (
                  <TextField
                    fullWidth
                    className={"mb-4 w-full " + (errorFields?.[key] ? 'error-border' : '')}
                    size="small"
                    variant="outlined"
                    name={key}
                    value={currentMcoDetails?.ERPstatus ?? ""}
                    type="text"
                    disabled={["responseFromErp"].includes(key)}
                  />
                )}
                {["ECOExportToERP"].includes(key) && currentMcoDetails && (
                  <Select
                    disabled={disableAllFields}
                    className={errorFields?.[key] ? 'error-border' : ''}
                    value={ECOExportToERP ?? "yes"}
                    onChange={(e) => {
                      let mcoPropeties = responseData?.mcoPropeties;
                      mcoPropeties["ECOExportToERP"] = e.target.value || null;
                      dispatch(updateMcoDetails({
                        mcoPropeties: mcoPropeties,
                      }));
                      dispatch(update_generalValues({
                        isPropertyFormEdited: true,
                      }));
                    }}
                    displayEmpty // Enable displayEmpty prop
                    renderValue={(selected) => {
                      if (!selected) {
                        return <em>--Select--</em>; // Placeholder text
                      }
                      return capitalize(ECOExportToERP || "yes");
                    }}
                    style={{ height: '37.13px', marginBottom: '1rem', width: "100%" }}
                  >
                    <MenuItem id="">Select</MenuItem>
                    {
                      ["yes", "no"].map((vitem) => {
                        return (
                          <MenuItem value={vitem} key={shortId.generate()}>{capitalize(vitem)}</MenuItem>
                        )
                      })
                    }
                  </Select>
                )}
                {
                  (!['startdate', 'rme', 'rsme', 'responseFromErp', 'ECOExportToERP'].includes(key)) && (
                    <TextField
                      className={errorFields?.[key] ? 'error-border' : ''}
                      fullWidth
                      size="small"
                      variant="outlined"
                      name={key}
                      // label={"Enter " + key}
                      value={
                        key === "plantName"
                          ? plantType
                          : key === "name"
                          ? selectedRoute?.title || ''
                          : responseData?.mcoPropeties[key] ?? ''
                      }
                      // value={responseData?.mcoPropeties[key] ?? ''}
                      onChange={(e) => {
                        let mcoPropeties = responseData.mcoPropeties;
                        if (![
                          "name",
                          "originator",
                          "businessUnit",
                          "mcostate",
                          "plantName",
                          "organizationId",
                        ].includes(e.target.name)) {
                          mcoPropeties[e.target.name] = e.target.value || null;
                          dispatch(updateMcoDetails({
                            mcoPropeties: mcoPropeties,
                          }));
                          dispatch(update_generalValues({
                            isPropertyFormEdited: true,
                          }));
                        }
                      }}
                      type="text"
                      disabled={[
                        "name",
                        "originator",
                        "businessUnit",
                        "mcostate",
                        "plantName",
                        "organizationId",
                      ].includes(key) || disableAllFields}
                    />
                  )
                }
              </Grid>

              {["responseFromErp"].includes(key) && currentMcoDetails && (
                <>
                  <Grid item md={3} sm={4} xs={12} className="font-medium">
                    <span></span>
                  </Grid>
                  <Grid item md={9} sm={8} xs={12} style={{ padding: "0px" }}>
                    {
                      (currentMcoDetails && (currentMcoDetails?.ERPstatus)?.toLowerCase() !== "success") ? (
                        <>
                          {
                            (currentMcoDetails?.ERPErrorDescription instanceof Array) ? (
                              <>
                                <ul>
                                  {
                                    currentMcoDetails?.ERPErrorDescription?.map((eitem) => {
                                      return <li key={shortId.generate()}>{trimmingDisplayContent(eitem)}</li>;
                                    })
                                  }
                                </ul>
                              </>
                            ) : (
                              <>
                                <ul>
                                  <li key={shortId.generate()}>{`${trimmingDisplayContent(currentMcoDetails?.ERPErrorDescription)}`}</li>
                                </ul>
                              </>
                            )
                          }
                        </>
                      ) : (null)
                    }
                    {/* <ul>
                      {
                        currentMcoDetails && currentMcoDetails?.ERPstatus !== "Success" &&
                        [currentMcoDetails?.ERPErrorDescription].length &&
                        [currentMcoDetails?.ERPErrorDescription]?.map((eitem) => {
                          return <li key={shortId.generate()}>{eitem}</li>;
                        })
                      }
                    </ul> */}
                  </Grid>
                </>
              )}
            </Grid>
          </>
        );
      })}

      {/* {[0].includes(tapvalue) && (
        <Button
          variant="contained"
          onClick={() => {
            const errors = validateForm('click');
            if (!Object.keys(errors).length) {
              submitPropertiesForm('form');
            }
          }}
          style={{ float: 'right', marginTop: '20px' }}
          data-disabled={isPropertyFormEdited}
          disabled={["Review", "Release", "Implemented"].includes(stepData)}
        >
          Save
        </Button>
      )} */}

      {openDesc && (
        <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
      )}
    </>
  )
}

PropertiesForm.propTypes = {
  tapvalue: PropTypes.number.isRequired,
  stepData: PropTypes.string.isRequired,
  submitPropertiesForm: PropTypes.func.isRequired,
};
export default PropertiesForm;


