import React from "react";
import { useSelector } from 'react-redux';
import { Button } from '@mui/material';

const withMaterialFormEdited = (WrappedComponent) => {
    return (props) => {
        const isMaterialFormEdited = useSelector((state) => state.mcoDetails.isMaterialFormEdited);
        return <WrappedComponent {...props} isMaterialFormEdited={isMaterialFormEdited} />;
    };
};
const ButtonComponent = (props) => {
    return (
        <div>
            <Button
                variant="contained"
                onClick={() => props?.bulkUploadPartandTemplate()}
                style={{ position: 'absolute', right: '165px', top: '0px', zIndex: 1 }}
                disabled={["Review", "Release", "Implemented"].includes(props?.stepData)}
            >
                Upload Part Templates
            </Button>
            <Button
                variant="contained"
                onClick={() => props?.exportMCOData()}
                style={{ position: 'absolute', right: '80px', top: '0px', zIndex: 1 }}
            >
                Export
            </Button>
            <Button
                variant="contained"
                onClick={() => props?.submitAffectedParts('form')}
                style={{ position: 'absolute', right: '0px', top: '0px', zIndex: 1 }}
                disabled={["Review", "Release", "Implemented"].includes(props?.stepData) || !props?.isMaterialFormEdited}
            >
                Save
            </Button>
        </div>
    );
};
const SaveButtons = withMaterialFormEdited(ButtonComponent);

export default SaveButtons;