import React from "react";
import { Link } from '@mui/material';

const TrackerHistoryLinks = ({ responseData, setShowTrackerBox, setShowHistoryBox }) => {
    return (
        <>
            <Link component="button" underline="none"
                onClick={() => {
                    setShowHistoryBox(true);
                }}
            >
                History
            </Link>

            <span style={{ padding: "10px" }}>|</span>

            {
                (responseData?.mcoPropeties?.mcostate?.toLowerCase() === "create") ? (
                    <Link component="button" underline="none"
                        disabled={true}
                        style={{ color: "grey" }}
                    >
                        Tracker
                    </Link>
                ) : (
                    <Link component="button" underline="none"
                        onClick={() => {
                            setShowTrackerBox(true);
                        }}
                        disabled={responseData?.mcoPropeties?.mcostate?.toLowerCase() === "create"}
                    >
                        Tracker
                    </Link>
                )
            }
        </>
    )
}

export default TrackerHistoryLinks;