import React from "react";
import { Button } from '@mui/material';

const RefreshButtons = ({ handleRefreshButtonFunc, responseData }) => {
    return (
        <div className="buttonRefreshContainer">
            <Button variant="contained" title="Refresh MCO data"
                onClick={() => {
                    handleRefreshButtonFunc(true, responseData)
                }}
                // disabled={((!lifeCycleData || !Object.keys(lifeCycleData)?.length) || responseData?.mcoPropeties?.mcostate?.toLowerCase() === "create")}
                className="refreshBtn"
            >
                Refresh
            </Button>
            <Button variant="contained" title="Reload Affected Items" className="reloadBtn"
                onClick={() => {
                    window.location.reload();
                }}>
                Reload Affected Items
            </Button>
        </div>
    )
}

export default RefreshButtons;