import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import Paper from '@mui/material/Paper';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Loader from 'app/components/Loader';
import { MCOService } from 'services/mcoService';

const columns = [
    {
        id: 'createdDateTime',
        label: 'Date',
        width: "120px"
    },
    {
        id: 'actionUserDetails',
        label: 'User',
        width: "120px"
    },
    {
        id: 'actionType',
        label: 'Action',
        width: "80px",
    },
    {
        id: 'state',
        label: 'State',
        width: "80px",
    },
    {
        id: 'description',
        label: 'Description',
        width: "400px",
    },
];

export default function ScrollableTable() {
    const [historyList, setHistoryList] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const [isHavingErrors, setIsHavingErrors] = useState(null);
    const mcoService = new MCOService();
    const params = useParams();

    const fetchHistoryList = async () => {
        try {
            await mcoService.getMcoHistorybyid([params?.mco_id]).then(async (histData) => {
                console.log(histData, "===histData");
                if (histData?.status === "success") {
                    if (Object.keys(histData?.data)?.length && Object.keys(histData?.data)?.includes(params?.mco_id)) {
                        setHistoryList(histData?.data?.[params?.mco_id]);
                    }
                } else {
                    setIsHavingErrors("No result found!");
                }
                setIsLoading(false);
            }).catch((error) => {
                throw error;
            });
        } catch (err) {
            throw err;
        }
    }

    useEffect(() => {
        fetchHistoryList();
    }, []);

    return (
        <Paper sx={{ maxWidth: "1200px", overflow: 'hidden' }}>
            <TableContainer sx={{ maxHeight: 440 }}>
                <Table stickyHeader aria-label="sticky table" style={{ padding: "0px 10px 10px" }}>
                    <TableHead>
                        <TableRow>
                            {columns.map((column) => (
                                <TableCell
                                    key={column.id}
                                    align={column.align}
                                    style={{ top: 0, minWidth: column.minWidth, width: column.width }}
                                >
                                    {column.label}
                                </TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {
                            historyList?.map((row) => (
                                <TableRow tabIndex={-1} key={row.code}>
                                    {columns.map((column) => {
                                        const value = row[column.id];
                                        return (
                                            <TableCell key={column.id} align={column.align}>
                                                {column.format && typeof value === 'number'
                                                    ? column.format(value)
                                                    : value}
                                            </TableCell>
                                        );
                                    })}
                                </TableRow>
                            ))
                        }
                    </TableBody>
                </Table>
                {
                    isLoading ? (
                        <div style={{ textAlign: 'center' }}>
                            <Loader show={isLoading} />
                        </div>
                    ) : (null)
                }
                {
                    (isHavingErrors) ? (
                        <div style={{ textAlign: 'center', padding: "10px 0px 20px" }}>
                            {isHavingErrors}
                        </div>
                    ) : (null)
                }
            </TableContainer>

        </Paper>
    );
}