import React, { useState, useEffect } from 'react';
import { Stepper, Step, StepLabel, StepContent, StepConnector, StepIcon } from '@mui/material';
import {
    Box,
    Paper,
    Typography,
    styled,
} from "@mui/material";
import CancelIcon from '@mui/icons-material/Cancel'; // This is the 'x' icon
import { useSelector } from 'react-redux';
import './mcoStyle.css';
import { trimmingDisplayContent } from "app/utils/mcoUtils/utilities";

let stepsLists = [
    {
        type: 'item',
        label: 'Item details send to ERP',
        description: "",
    },
    {
        type: 'bos',
        label: 'Bill of specification details send to ERP',
        description: "",
    },
    {
        type: 'bom',
        label: 'ERP Change Order Creation',
        description: "",
    },
    {
        type: 'release',
        label: 'Update Enovia MCO Lifecycle to Release',
        description: "MCO release is in progress. This process will take some time, so please refresh after a while.",
    },
];

// Customizing the StepIcon to display an empty circle with a border
const CustomStepIcon = styled(StepIcon)(({ theme, completed }) => ({
    width: 25, // Adjust size of the icon
    height: 25, // Adjust size of the icon
    border: `2px solid ${completed ? theme.palette.primary.main : theme.palette.grey[500]}`, // Border color based on completion status
    borderRadius: '50%', // Make it a circle
}));

// Customizing the StepConnector to change its color
const StyledStepConnector = styled(StepConnector, { shouldForwardProp: (prop) => prop !== 'completed' })(({ theme, completed }) => ({
    // Styling for incomplete steps
    '& .MuiStepConnector-line': {
        borderColor: theme.palette.grey[500], // Default color for incomplete steps
    },
    // Styling for completed steps
    ...(completed && {
        '& .MuiStepConnector-line': {
            borderColor: theme.palette.primary.main, // Change this to the desired color for completed steps
        },
    }),
}));

export default function VerticalLinearStepper({ activeStepValue, currentMCOTrackerdetails = {} }) {
    const { responseData } = useSelector((state) => state.mcoDetails);
    const [activeStep, setActiveStep] = useState(-1);
    const [activeStatus, setActiveStatus] = useState("inprogress");
    const [steppersLists, setSteppersLists] = useState(stepsLists);
    const completedSteps = [0];

    useEffect(() => {
        // console.log(stepsLists, "======stepsLists");
        // console.log(activeStepValue, "======activeStepValue");
        // console.log(currentMCOTrackerdetails, "======currentMCOTrackerdetails");

        if (Object.keys(activeStepValue || {}).length) {
            const mcoStateVal = responseData?.mcoPropeties?.mcostate?.toLowerCase();
            // const newstepsLists = stepsLists?.filter(sfItem => {
            //     return !activeStepValue?.skippedstage?.includes(sfItem?.type)
            // }).map(citem => {
            //     if (!["release"].includes(citem?.type)) {
            //         citem.description = currentMCOTrackerdetails[`${citem?.type}Message`];
            //     }
            //     if (["release"].includes(mcoStateVal)) {
            //         citem.description = "";
            //     }
            //     return citem;
            // });

            const newstepsLists = stepsLists?.map(citem => {
                if (activeStepValue?.skippedstage?.includes(citem?.type) && !citem?.label?.includes('Skipped')) {
                    citem.label = `${citem.label} - Skipped not required`;
                }
                if (!["release"]?.includes(citem?.type)) {
                    citem.description = currentMCOTrackerdetails[`${citem?.type}Message`];
                }
                if (["failed"]?.includes(currentMCOTrackerdetails[`${citem?.type}Status`])) {
                    citem.description = currentMCOTrackerdetails[`${citem?.type}Message`];
                } else if (["release"]?.includes(mcoStateVal)) {
                    citem.description = "";
                }
                return citem;
            });
            setSteppersLists(newstepsLists);
            setActiveStep(activeStepValue?.stepperValue);
            setActiveStatus(activeStepValue?.currentStatus?.status);

            // if (!["release"]?.includes(mcoStateVal) && ["failed"]?.includes(currentMCOTrackerdetails[`releaseStatus`])) {
            //     setActiveStatus("failed");
            // } else {
            //     setActiveStatus(activeStepValue?.currentStatus?.status);
            // }
        }
    }, [activeStepValue]);

    return (
        <Box sx={{ maxWidth: 400 }}>
            <Stepper activeStep={activeStep}
                orientation="vertical"
                className='stepperContainer'
            >
                {steppersLists.map((step, index) => (
                    <Step
                        key={step.label}
                    >
                        <StepLabel
                            // optional={
                            //     index === 2 ? (
                            //         <Typography variant="caption">Last step</Typography>
                            //     ) : null
                            // }
                            className={`stepperSpace ${activeStatus} ${(activeStepValue?.skippedstage?.includes(step?.type) ? "skippedStep" : "")}`}
                            StepIconComponent={(props) => {
                                const { active } = props;
                                // console.log(props, '=====');
                                // console.log(step, '=====');
                                // console.log(activeStepValue?.skippedstage, '=====3');
                                if (active && ["failed"]?.includes(activeStatus)) {
                                    return <CancelIcon sx={{ color: 'red' }} /> // Customize color if needed
                                } else {
                                    return <CustomStepIcon {...props} />
                                }
                            }}
                        >
                            {step.label}
                        </StepLabel>
                        <StepContent className={`stepperContentSpace ${activeStatus}`}>
                            <Typography>{trimmingDisplayContent(step.description)}</Typography>
                        </StepContent>

                        {/* Pass step information to CustomStepConnector */}
                        <StyledStepConnector className={`${activeStatus}`} completed={completedSteps?.includes(index)} />
                    </Step>
                ))}
            </Stepper>
            {
                activeStep === steppersLists.length && (
                    <Paper square elevation={0} sx={{ p: 3 }} style={{ textAlign: "left" }}>
                        <Typography>All steps were successfully completed!</Typography>
                    </Paper>
                )
            }
        </Box >
    );
}
