import { useEffect, useState } from 'react';
import { useSelector, shallowEqual } from 'react-redux';
import Stack from '@mui/material/Stack';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import Link from '@mui/material/Link';
import { Grid } from "@mui/material";
import RotatingInProgressIcon from './InprogressIcon';

const actionTextVar = {
    promote: "Promote >>",
    demote: "<< Demote"
}

export default function LifeCycleStepperComponent(props) {
    const { responseData, lifeCycleData } = useSelector((state) => ({
        responseData: state.mcoDetails?.responseData,
        lifeCycleData: state.mcoDetails.lifeCycleData,
    }), shallowEqual);
    const { stepData, lifeCycleStep } = props;
    const [activeStep, setactiveStep] = useState('Create');
    let instance = localStorage.getItem('instance');
    let steps = [];
    if(instance === 'SaaS'){
        steps = [
            'Prepare',
            'In Work',
            'In Approval',
            'Approved',
            'Complete'
        ];
    } else {
        steps = [
            'Create',
            'Review',
            'Release',
            // 'Release Item',
            // 'Release BOM',
            'Implemented'
        ];
    }

    useEffect(() => {
        setactiveStep(stepData);
    }, [stepData])

    return (
        <Grid item md={12} sm={12} xs={12} className="font-medium">
            <div className="" style={{ margin: 'auto auto 25px auto', textAlign: 'center', width: '75%', position: "relative" }}>
                <Stack sx={{ width: '100%' }} spacing={4}>
                    {
                        (responseData?.mcoPropeties?.mcostate?.toLowerCase() === "review" && (lifeCycleData?.[responseData?.mcoPropeties?.name]?.promoted === "inprogress" || ["Item", "BOM"].includes(lifeCycleStep))) ? (
                            <><RotatingInProgressIcon title={lifeCycleData?.[responseData?.mcoPropeties?.name]?.promoted} /></>
                        ) : (null)
                    }
                    <Stepper alternativeLabel activeStep={1} style={{ marginTop: "0px" }}>
                        {steps.map((label, sindex) => (
                            <Step key={label}>
                                {
                                    (activeStep === label) ? (
                                        <div style={{
                                            width: 'auto',
                                            minWidth: '110px',
                                            display: 'inline-table',
                                            padding: '10px',
                                            color: '#ffffff',
                                            background: '#42a2da',
                                            zIndex: '1',
                                            position: 'relative',
                                            top: '-10px',
                                            // cursor: 'pointer',
                                            borderRadius: '-10px',
                                            boxShadow: '0px 2px 2px 0px rgba(0, 0, 0, 0.3)',
                                        }} data-state={label}>
                                            {
                                                (sindex !== 0) && (
                                                    <span style={{
                                                        width: '0',
                                                        height: '0',
                                                        borderStyle: 'solid',
                                                        borderTop: '7px solid transparent',
                                                        borderBottom: '7px solid transparent',
                                                        borderLeft: '10px solid #bdbdbd',
                                                        borderRight: '0',
                                                        position: 'absolute',
                                                        left: '-10px',
                                                        top: '15.5px',
                                                    }}></span>
                                                )
                                            }

                                            {label}
                                        </div>
                                    ) : (
                                        <div style={{
                                            width: 'auto',
                                            minWidth: '110px',
                                            display: 'inline-table',
                                            padding: '10px',
                                            color: '#5c5c5c',
                                            background: '#e6e7e8',
                                            zIndex: '1',
                                            position: 'relative',
                                            top: '-10px',
                                            // cursor: 'pointer',
                                            borderRadius: '-10px',
                                            boxShadow: '0px 2px 2px 0px rgba(0, 0, 0, 0.3)',
                                        }} data-state={label}>
                                            {
                                                (sindex !== 0) && (
                                                    <span style={{
                                                        width: '0',
                                                        height: '0',
                                                        borderStyle: 'solid',
                                                        borderTop: '7px solid transparent',
                                                        borderBottom: '7px solid transparent',
                                                        borderLeft: '10px solid #bdbdbd',
                                                        borderRight: '0',
                                                        position: 'absolute',
                                                        left: '-10px',
                                                        top: '15.5px',
                                                    }}></span>
                                                )
                                            }
                                            {label}
                                        </div>
                                    )
                                }
                                {
                                    (activeStep === label) && (
                                        <div>
                                            {
                                                (!["Item", "BOM"].includes(lifeCycleStep) && ['Release'].includes(activeStep)) && (
                                                    <Link
                                                        component="button"
                                                        variant="body2"
                                                        underline="none"
                                                        title={actionTextVar.promote}
                                                        onClick={() => {
                                                            props.handleLifeCycle(label, 'promote')
                                                        }}
                                                    >
                                                        {actionTextVar.promote}
                                                    </Link>
                                                )
                                            }
                                            {
                                                (["Item", "BOM"].includes(lifeCycleStep) && ['Review', 'Release', 'Implemented', 'In Work', 'In Approval', 'Approved'].includes(activeStep)) && (
                                                    <Link
                                                        component="button"
                                                        variant="body2"
                                                        underline="none"
                                                        title={actionTextVar.promote}
                                                        style={{ color: 'lightgrey' }}
                                                    >
                                                        {actionTextVar.promote}
                                                    </Link>
                                                )
                                            }

                                            {
                                                (!["Item", "BOM"].includes(lifeCycleStep) && ['Review', 'In Work', 'In Approval', 'Approved',].includes(activeStep)) && (
                                                    <>
                                                        <div>
                                                            <Link
                                                                component="button"
                                                                variant="body2"
                                                                underline="none"
                                                                title={actionTextVar.promote}
                                                                onClick={() => {
                                                                    props.handleLifeCycle(label, 'promote')
                                                                }}
                                                            >
                                                                {actionTextVar.promote}
                                                            </Link>
                                                        </div>
                                                        <div>
                                                            <Link
                                                                component="button"
                                                                variant="body2"
                                                                underline="none"
                                                                title={actionTextVar.demote}
                                                                onClick={() => {
                                                                    props.demoteState(label, 'demote')
                                                                }}
                                                            >
                                                                {actionTextVar.demote}
                                                            </Link>
                                                        </div>
                                                    </>
                                                )
                                            }

                                            {
                                                (['Create', 'Prepare'].includes(activeStep)) && (
                                                    <Link
                                                        component="button"
                                                        variant="body2"
                                                        underline="none"
                                                        title={actionTextVar.promote}
                                                        onClick={() => {
                                                            props.handleLifeCycle(label, 'promote')
                                                        }}
                                                    >
                                                        {actionTextVar.promote}
                                                    </Link>
                                                )
                                            }

                                        </div>
                                    )
                                }

                            </Step>
                        ))}
                    </Stepper>
                </Stack >
            </div>
        </Grid>
    );
}
